import React, {


  useState,
  useContext,

} from 'react';

import { useConfig } from './useConfig';






export const CurrencyContext = React.createContext

(undefined);

export const CurrencyProvider = ({ children }) => {
  const config = useConfig();
  const currencies = config.currencies;
  const engineers = currencies.find(currency => currency.kind === null);
  const [currency, setCurrency] = useState(
    engineers || currencies[0],
  );
  return (
    React.createElement(CurrencyContext.Provider, { value: { currency, setCurrency },}
      , children
    )
  );
};

export function useCurrency() {
  const context = useContext(CurrencyContext);

  if (!context) {
    assertNever();
  }

  return [context.currency, context.setCurrency];
}

function assertNever() {
  throw Error('Cannot use useCurrency outside of CurrencyProvider');
}
