// retried, canceled, infrastructure_fail, timedout, not_run, running, failed, queued, scheduled, not_running, no_tests, fixed, success
const getStatusComponent = (status = '') => {
  switch (status.toLocaleLowerCase('en-US')) {
    case 'queued':
    case 'scheduled':
      return React.createElement(StatusPending, null );
    case 'running':
      return React.createElement(StatusRunning, null );
    case 'failed':
      return React.createElement(StatusError, null );
    case 'success':
      return React.createElement(StatusOK, null );
    case 'canceled':
    default:
      return React.createElement(StatusWarning, null );
  }
};

