import { useEntity } from '@backstage/plugin-catalog-react';

import {
  Box,
  Card,
  CardContent,
  CardHeader,
  makeStyles,
  Typography,
} from '@material-ui/core';
import TrendingDownIcon from '@material-ui/icons/TrendingDown';
import TrendingFlatIcon from '@material-ui/icons/TrendingFlat';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import { Alert } from '@material-ui/lab';

import React from 'react';
import useAsync from 'react-use/lib/useAsync';
import {
  CartesianGrid,
  Legend,
  Line,
  LineChart,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
} from 'recharts';
import { codeCoverageApiRef } from '../../api';

import { Progress, ResponseErrorPanel } from '@backstage/core-components';
import { useApi } from '@backstage/core-plugin-api';

import { DateTime } from 'luxon';



const useStyles = makeStyles(theme => ({
  trendDown: {
    color: theme.palette.status.warning,
  },
  trendUp: {
    color: theme.palette.status.ok,
  },
}));

const getTrendIcon = (trend, classes) => {
  switch (true) {
    case trend > 0:
      return React.createElement(TrendingUpIcon, { className: classes.trendUp,} );
    case trend < 0:
      return React.createElement(TrendingDownIcon, { className: classes.trendDown,} );
    case trend === 0:
    default:
      return React.createElement(TrendingFlatIcon, null );
  }
};

// convert timestamp to human friendly form
function formatDateToHuman(timeStamp) {
  return DateTime.fromMillis(Number(timeStamp)).toLocaleString(
    DateTime.DATETIME_MED,
  );
}

export const CoverageHistoryChart = () => {
  const { entity } = useEntity();
  const codeCoverageApi = useApi(codeCoverageApiRef);
  const {
    loading: loadingHistory,
    error: errorHistory,
    value: valueHistory,
  } = useAsync(
    async () =>
      await codeCoverageApi.getCoverageHistoryForEntity({
        kind: entity.kind,
        namespace: entity.metadata.namespace || 'default',
        name: entity.metadata.name,
      }),
  );
  const classes = useStyles();

  if (loadingHistory) {
    return React.createElement(Progress, null );
  }
  if (errorHistory) {
    return React.createElement(ResponseErrorPanel, { error: errorHistory,} );
  } else if (!valueHistory) {
    return React.createElement(Alert, { severity: "warning",}, "No history found."  );
  }

  if (!valueHistory.history.length) {
    return (
      React.createElement(Card, null
        , React.createElement(CardHeader, { title: "History",} )
        , React.createElement(CardContent, null, "No coverage history found"   )
      )
    );
  }

  const oldestCoverage = valueHistory.history[0];
  const [latestCoverage] = valueHistory.history.slice(-1);

  const getTrendForCoverage = (type) => {
    if (!oldestCoverage[type].percentage) {
      return 0;
    }
    return (
      ((latestCoverage[type].percentage - oldestCoverage[type].percentage) /
        oldestCoverage[type].percentage) *
      100
    );
  };

  const lineTrend = getTrendForCoverage('line');
  const branchTrend = getTrendForCoverage('branch');

  return (
    React.createElement(Card, null
      , React.createElement(CardHeader, { title: "History",} )
      , React.createElement(CardContent, null
        , React.createElement(Box, { px: 6, display: "flex",}
          , React.createElement(Box, { display: "flex", mr: 4,}
            , getTrendIcon(lineTrend, classes)
            , React.createElement(Typography, null, "Current line: "
                , latestCoverage.line.percentage, "%", React.createElement('br', null ), "("
              , Math.floor(lineTrend), "% change over "   , valueHistory.history.length, ' ', "builds)"

            )
          )
          , React.createElement(Box, { display: "flex",}
            , getTrendIcon(branchTrend, classes)
            , React.createElement(Typography, null, "Current branch: "
                , latestCoverage.branch.percentage, "%", React.createElement('br', null ), "("
              , Math.floor(branchTrend), "% change over"  , ' '
              , valueHistory.history.length, " builds)"
            )
          )
        )
        , React.createElement(ResponsiveContainer, { width: "100%", height: 300,}
          , React.createElement(LineChart, {
            data: valueHistory.history,
            margin: { right: 48, top: 32 },}
          
            , React.createElement(CartesianGrid, { strokeDasharray: "3 3" ,} )
            , React.createElement(XAxis, {
              dataKey: "timestamp",
              tickFormatter: formatDateToHuman,
              reversed: true,}
            )
            , React.createElement(YAxis, { dataKey: "line.percentage",} )
            , React.createElement(YAxis, { dataKey: "branch.percentage",} )
            , React.createElement(Tooltip, { labelFormatter: formatDateToHuman,} )
            , React.createElement(Legend, null )
            , React.createElement(Line, {
              type: "monotone",
              dataKey: "branch.percentage",
              stroke: "#8884d8",}
            )
            , React.createElement(Line, { type: "monotone", dataKey: "line.percentage", stroke: "#82ca9d",} )
          )
        )
      )
    )
  );
};
