import React from 'react';
import { List } from '@material-ui/core';
import { EmptyProviders } from './EmptyProviders';
import { DefaultProviderSettings } from './DefaultProviderSettings';

import { configApiRef, useApi } from '@backstage/core-plugin-api';
import { InfoCard } from '@backstage/core-components';





export const UserSettingsAuthProviders = ({ providerSettings }) => {
  const configApi = useApi(configApiRef);
  const providersConfig = configApi.getOptionalConfig('auth.providers');
  const configuredProviders = providersConfig?.keys() || [];
  const providers = providerSettings ?? (
    React.createElement(DefaultProviderSettings, { configuredProviders: configuredProviders,} )
  );

  if (!providerSettings && !configuredProviders?.length) {
    return React.createElement(EmptyProviders, null );
  }

  return (
    React.createElement(InfoCard, { title: "Available Providers" ,}
      , React.createElement(List, { dense: true,}, providers)
    )
  );
};
