
















import { Card, CardContent, CardHeader } from '@material-ui/core';
import React from 'react';
import YAML from 'yaml';
import { CodeSnippet } from '@backstage/core-components';
import { trimEnd } from 'lodash';
import { useCatalogFilename } from '../../hooks';

/**
 * Props for {@link PreviewCatalogInfoComponent}.
 *
 * @public
 */






/**
 * Previews information about an entity to create.
 *
 * @public
 */
export const PreviewCatalogInfoComponent = (
  props,
) => {
  const { repositoryUrl, entities, classes } = props;
  const catalogFilename = useCatalogFilename();

  return (
    React.createElement(Card, { variant: "outlined", className: classes?.card,}
      , React.createElement(CardHeader, {
        title: 
          React.createElement('code', null, `${trimEnd(repositoryUrl, '/')}/${catalogFilename}`)
        ,}
      )

      , React.createElement(CardContent, { className: classes?.cardContent,}
        , React.createElement(CodeSnippet, {
          text: entities
            .map(e => YAML.stringify(e))
            .join('---\n')
            .trim(),
          language: "yaml",}
        )
      )
    )
  );
};
