import { useEntity } from '@backstage/plugin-catalog-react';
import { makeStyles, Paper } from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import React from 'react';
import useAsync from 'react-use/lib/useAsync';
import { codeCoverageApiRef } from '../../api';

import { CodeRow } from './CodeRow';
import { highlightLines } from './Highlighter';

import { Progress, ResponseErrorPanel } from '@backstage/core-components';
import { useApi } from '@backstage/core-plugin-api';






const useStyles = makeStyles(theme => ({
  paper: {
    margin: 'auto',
    top: '2em',
    width: '80%',
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    overflow: 'scroll',
  },
  coverageFileViewTable: {
    borderSpacing: '0px',
    width: '80%',
    marginTop: theme.spacing(2),
  },
}));





const FormattedLines = ({
  highlightedLines,
  lineHits,
}) => {
  return (
    React.createElement(React.Fragment, null
      , highlightedLines.map((lineContent, idx) => {
        const line = idx + 1;
        return (
          React.createElement(CodeRow, {
            key: line,
            lineNumber: line,
            lineContent: lineContent,
            lineHits: lineHits[line],}
          )
        );
      })
    )
  );
};

export const FileContent = ({ filename, coverage }) => {
  const { entity } = useEntity();
  const codeCoverageApi = useApi(codeCoverageApiRef);
  const { loading, error, value } = useAsync(
    async () =>
      await codeCoverageApi.getFileContentFromEntity(
        {
          kind: entity.kind,
          namespace: entity.metadata.namespace || 'default',
          name: entity.metadata.name,
        },
        filename,
      ),
    [entity],
  );

  const classes = useStyles();

  if (loading) {
    return React.createElement(Progress, null );
  }
  if (error) {
    return React.createElement(ResponseErrorPanel, { error: error,} );
  }
  if (!value) {
    return (
      React.createElement(Alert, { severity: "warning",}, "Unable to retrieve file content for "
              , filename
      )
    );
  }

  const [language] = filename.split('.').slice(-1);
  const highlightedLines = highlightLines(language, value.split('\n'));

  // List of formatted nodes containing highlighted code
  // lineHits array where lineHits[i] is number of hits for line i + 1
  const lineHits = Object.entries(coverage.lineHits).reduce(
    (acc, next) => {
      acc[next[0]] = next[1];
      return acc;
    },
    {},
  );

  return (
    React.createElement(Paper, { variant: "outlined", className: classes.paper,}
      , React.createElement('table', { className: classes.coverageFileViewTable,}
        , React.createElement('tbody', null
          , React.createElement(FormattedLines, {
            highlightedLines: highlightedLines,
            lineHits: lineHits,}
          )
        )
      )
    )
  );
};
