






















export class CloudbuildClient  {
  constructor(  googleAuthApi) {;this.googleAuthApi = googleAuthApi;}

  async reRunWorkflow({
    projectId,
    runId,
  }


) {
    await fetch(
      `https://cloudbuild.googleapis.com/v1/projects/${encodeURIComponent(
        projectId,
      )}/builds/${encodeURIComponent(runId)}:retry`,
      {
        headers: new Headers({
          Accept: '*/*',
          Authorization: `Bearer ${await this.getToken()}`,
        }),
      },
    );
  }
  async listWorkflowRuns({
    projectId,
  }

) {
    const workflowRuns = await fetch(
      `https://cloudbuild.googleapis.com/v1/projects/${encodeURIComponent(
        projectId,
      )}/builds`,
      {
        headers: new Headers({
          Accept: '*/*',
          Authorization: `Bearer ${await this.getToken()}`,
        }),
      },
    );

    const builds =
      await workflowRuns.json();

    return builds;
  }
  async getWorkflow({
    projectId,
    id,
  }


) {
    const workflow = await fetch(
      `https://cloudbuild.googleapis.com/v1/projects/${encodeURIComponent(
        projectId,
      )}/builds/${encodeURIComponent(id)}`,
      {
        headers: new Headers({
          Accept: '*/*',
          Authorization: `Bearer ${await this.getToken()}`,
        }),
      },
    );

    const build = await workflow.json();

    return build;
  }
  async getWorkflowRun({
    projectId,
    id,
  }


) {
    const workflow = await fetch(
      `https://cloudbuild.googleapis.com/v1/projects/${encodeURIComponent(
        projectId,
      )}/builds/${encodeURIComponent(id)}`,
      {
        headers: new Headers({
          Accept: '*/*',
          Authorization: `Bearer ${await this.getToken()}`,
        }),
      },
    );
    const build = await workflow.json();

    return build;
  }

  async getToken() {
    // NOTE(freben - gcp-projects): There's a .read-only variant of this scope that we could
    // use for readonly operations, but that means we would ask the user for a
    // second auth during creation and I decided to keep the wider scope for
    // all ops for now
    return this.googleAuthApi.getAccessToken(
      'https://www.googleapis.com/auth/cloud-platform',
    );
  }
}
