import React, {
  useState,
  forwardRef,


} from 'react';
import { Checkbox, FormControl, FormControlLabel } from '@material-ui/core';




export const AlertAcceptForm = forwardRef


(({ onSubmit, disableSubmit }, ref) => {
  const [checked, setChecked] = useState(false);

  const onFormSubmit = e => {
    e.preventDefault();
    onSubmit(null);
  };

  const onChecked = (_, isChecked) => {
    setChecked(isChecked);
    disableSubmit(!isChecked);
  };

  return (
    React.createElement('form', { ref: ref, onSubmit: onFormSubmit,}
      , React.createElement(FormControl, { component: "fieldset", fullWidth: true,}
        , React.createElement(FormControlLabel, {
          label: "My team can commit to making this change soon, or has already."           ,
          value: checked,
          control: 
            React.createElement(Checkbox, { color: "primary", checked: checked, onChange: onChecked,} )
          ,}
        )
      )
    )
  );
});
