import React from 'react';
import capitalize from 'lodash/capitalize';
import { Progress } from '@backstage/core-components';
import {
  Box,
  Checkbox,
  FormControlLabel,
  TextField,
  Typography,
} from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { Autocomplete } from '@material-ui/lab';
import { useEntityTypeFilter } from '@backstage/plugin-catalog-react';
import { alertApiRef, useApi } from '@backstage/core-plugin-api';

const icon = React.createElement(CheckBoxOutlineBlankIcon, { fontSize: "small",} );
const checkedIcon = React.createElement(CheckBoxIcon, { fontSize: "small",} );

/**
 * The component to select the `type` of `Template` that you will see in the table.
 *
 * @public
 */
export const TemplateTypePicker = () => {
  const alertApi = useApi(alertApiRef);
  const { error, loading, availableTypes, selectedTypes, setSelectedTypes } =
    useEntityTypeFilter();

  if (loading) return React.createElement(Progress, null );

  if (!availableTypes) return null;

  if (error) {
    alertApi.post({
      message: `Failed to load entity types`,
      severity: 'error',
    });
    return null;
  }

  return (
    React.createElement(Box, { pb: 1, pt: 1,}
      , React.createElement(Typography, { variant: "button",}, "Categories")
      , React.createElement(Autocomplete, {
        multiple: true,
        'aria-label': "Categories",
        options: availableTypes,
        value: selectedTypes,
        onChange: (_, value) => setSelectedTypes(value),
        renderOption: (option, { selected }) => (
          React.createElement(FormControlLabel, {
            control: 
              React.createElement(Checkbox, {
                icon: icon,
                checkedIcon: checkedIcon,
                checked: selected,}
              )
            ,
            label: capitalize(option),}
          )
        ),
        size: "small",
        popupIcon: React.createElement(ExpandMoreIcon, { 'data-testid': "categories-picker-expand",} ),
        renderInput: params => React.createElement(TextField, { ...params, variant: "outlined",} ),}
      )
    )
  );
};
