import {
  Link,
  Typography,
  Box,
  IconButton,
  Tooltip,
  Button,
} from '@material-ui/core';
import RetryIcon from '@material-ui/icons/Replay';
import GitHubIcon from '@material-ui/icons/GitHub';
import { Link as RouterLink } from 'react-router-dom';
import { useWorkflowRuns, } from '../useWorkflowRuns';
import { WorkflowRunStatus } from '../WorkflowRunStatus';
import SyncIcon from '@material-ui/icons/Sync';
import { buildRouteRef } from '../../routes';
import { getProjectNameFromEntity } from '../getProjectNameFromEntity';

import { readGitHubIntegrationConfigs } from '@backstage/integration';

import { EmptyState, Table, } from '@backstage/core-components';
import { configApiRef, useApi, useRouteRef } from '@backstage/core-plugin-api';

const generatedColumns = [
  {
    title: 'ID',
    field: 'id',
    type: 'numeric',
    width: '150px',
  },
  {
    title: 'Message',
    field: 'message',
    highlight: true,
    render: (row) => {
      const LinkWrapper = () => {
        const routeLink = useRouteRef(buildRouteRef);
        return (
          React.createElement(Link, { component: RouterLink, to: routeLink({ id: row.id }),}
            , row.message
          )
        );
      };

      return React.createElement(LinkWrapper, null );
    },
  },
  {
    title: 'Source',
    render: (row) => (
      React.createElement(Typography, { variant: "body2", noWrap: true,}
        , React.createElement('p', null, row.source?.branchName)
        , React.createElement('p', null, row.source?.commit.hash)
      )
    ),
  },
  {
    title: 'Workflow',
    field: 'workflowName',
  },
  {
    title: 'Status',
    width: '150px',

    render: (row) => (
      React.createElement(Box, { display: "flex", alignItems: "center",}
        , React.createElement(WorkflowRunStatus, { status: row.status, conclusion: row.conclusion,} )
      )
    ),
  },
  {
    title: 'Actions',
    render: (row) => (
      React.createElement(Tooltip, { title: "Rerun workflow" ,}
        , React.createElement(IconButton, { onClick: row.onReRunClick,}
          , React.createElement(RetryIcon, null )
        )
      )
    ),
    width: '10%',
  },
];













export const WorkflowRunsTableView = ({
  projectName,
  loading,
  pageSize,
  page,
  retry,
  runs,
  onChangePage,
  onChangePageSize,
  total,
}) => {
  return (
    React.createElement(Table, {
      isLoading: loading,
      options: { paging: true, pageSize, padding: 'dense' },
      totalCount: total,
      page: page,
      actions: [
        {
          icon: () => React.createElement(SyncIcon, null ),
          tooltip: 'Reload workflow runs',
          isFreeAction: true,
          onClick: () => retry(),
        },
      ],
      data: runs ?? [],
      onPageChange: onChangePage,
      onRowsPerPageChange: onChangePageSize,
      style: { width: '100%' },
      title: 
        React.createElement(Box, { display: "flex", alignItems: "center",}
          , React.createElement(GitHubIcon, null )
          , React.createElement(Box, { mr: 1,} )
          , React.createElement(Typography, { variant: "h6",}, projectName)
        )
      ,
      columns: generatedColumns,}
    )
  );
};

export const WorkflowRunsTable = ({
  entity,
  branch,
}


) => {
  const config = useApi(configApiRef);
  const projectName = getProjectNameFromEntity(entity);
  // TODO: Get github hostname from metadata annotation
  const hostname = readGitHubIntegrationConfigs(
    config.getOptionalConfigArray('integrations.github') ?? [],
  )[0].host;
  const [owner, repo] = (projectName ?? '/').split('/');
  const [{ runs, ...tableProps }, { retry, setPage, setPageSize }] =
    useWorkflowRuns({
      hostname,
      owner,
      repo,
      branch,
    });

  const githubHost = hostname || 'github.com';
  const hasNoRuns = !tableProps.loading && !runs;

  return hasNoRuns ? (
    React.createElement(EmptyState, {
      missing: "data",
      title: "No Workflow Data"  ,
      description: "This component has GitHub Actions enabled, but no data was found. Have you created any Workflows? Click the button below to create a new Workflow."                        ,
      action: 
        React.createElement(Button, {
          variant: "contained",
          color: "primary",
          href: `https://${githubHost}/${projectName}/actions/new`,}
        , "Create new Workflow"

        )
      ,}
    )
  ) : (
    React.createElement(WorkflowRunsTableView, {
      ...tableProps,
      runs: runs,
      loading: tableProps.loading,
      retry: retry,
      onChangePageSize: setPageSize,
      onChangePage: setPage,}
    )
  );
};
