import React, { } from 'react';
import {
  makeStyles,
  FormControl,
  FormControlLabel,
  InputLabel,
  Checkbox,
  Select,
  MenuItem,
  FormLabel,
} from '@material-ui/core';

import {
  AutocompleteFilter,

} from './SearchFilter.Autocomplete';
import { useSearch } from '@backstage/plugin-search-react';
import { useAsyncFilterValues, useDefaultFilterValue } from './hooks';

const useStyles = makeStyles({
  label: {
    textTransform: 'capitalize',
  },
});

/**
 * @public
 */



























const CheckboxFilter = (props) => {
  const {
    className,
    defaultValue,
    label,
    name,
    values: givenValues = [],
    valuesDebounceMs,
  } = props;
  const classes = useStyles();
  const { filters, setFilters } = useSearch();
  useDefaultFilterValue(name, defaultValue);
  const asyncValues =
    typeof givenValues === 'function' ? givenValues : undefined;
  const defaultValues =
    typeof givenValues === 'function' ? undefined : givenValues;
  const { value: values = [], loading } = useAsyncFilterValues(
    asyncValues,
    '',
    defaultValues,
    valuesDebounceMs,
  );

  const handleChange = (e) => {
    const {
      target: { value, checked },
    } = e;

    setFilters(prevFilters => {
      const { [name]: filter, ...others } = prevFilters;
      const rest = ((filter ) || []).filter(i => i !== value);
      const items = checked ? [...rest, value] : rest;
      return items.length ? { ...others, [name]: items } : others;
    });
  };

  return (
    React.createElement(FormControl, {
      className: className,
      disabled: loading,
      fullWidth: true,
      'data-testid': "search-checkboxfilter-next",}
    
      , label ? React.createElement(FormLabel, { className: classes.label,}, label) : null
      , values.map((value) => (
        React.createElement(FormControlLabel, {
          key: value,
          control: 
            React.createElement(Checkbox, {
              color: "primary",
              tabIndex: -1,
              inputProps: { 'aria-labelledby': value },
              value: value,
              name: value,
              onChange: handleChange,
              checked: ((filters[name] ) ?? []).includes(value),}
            )
          ,
          label: value,}
        )
      ))
    )
  );
};

const SelectFilter = (props) => {
  const {
    className,
    defaultValue,
    label,
    name,
    values: givenValues,
    valuesDebounceMs,
  } = props;
  const classes = useStyles();
  useDefaultFilterValue(name, defaultValue);
  const asyncValues =
    typeof givenValues === 'function' ? givenValues : undefined;
  const defaultValues =
    typeof givenValues === 'function' ? undefined : givenValues;
  const { value: values = [], loading } = useAsyncFilterValues(
    asyncValues,
    '',
    defaultValues,
    valuesDebounceMs,
  );
  const { filters, setFilters } = useSearch();

  const handleChange = (e) => {
    const {
      target: { value },
    } = e;

    setFilters(prevFilters => {
      const { [name]: filter, ...others } = prevFilters;
      return value ? { ...others, [name]: value  } : others;
    });
  };

  return (
    React.createElement(FormControl, {
      disabled: loading,
      className: className,
      variant: "filled",
      fullWidth: true,
      'data-testid': "search-selectfilter-next",}
    
      , label ? (
        React.createElement(InputLabel, { className: classes.label, margin: "dense",}
          , label
        )
      ) : null
      , React.createElement(Select, {
        variant: "outlined",
        value: filters[name] || '',
        onChange: handleChange,}
      
        , React.createElement(MenuItem, { value: "",}
          , React.createElement('em', null, "All")
        )
        , values.map((value) => (
          React.createElement(MenuItem, { key: value, value: value,}
            , value
          )
        ))
      )
    )
  );
};

const SearchFilter = ({
  component: Element,
  ...props
}) => React.createElement(Element, { ...props,} );

SearchFilter.Checkbox = (
  props
,
) => React.createElement(SearchFilter, { ...props, component: CheckboxFilter,} );

SearchFilter.Select = (
  props
,
) => React.createElement(SearchFilter, { ...props, component: SelectFilter,} );

/**
 * A control surface for a given filter field name, rendered as an autocomplete
 * textfield. A hard-coded list of values may be provided, or an async function
 * which returns values may be provided instead.
 * @public
 */
SearchFilter.Autocomplete = (props) => (
  React.createElement(SearchFilter, { ...props, component: AutocompleteFilter,} )
);

/**
 * @deprecated This component was used for rapid prototyping of the Backstage
 * Search platform. Now that the API has stabilized, you should use the
 * <SearchFilter /> component instead. This component will be removed in an
 * upcoming release.
 */
const SearchFilterNext = SearchFilter;

export { SearchFilter, SearchFilterNext };
