export const ErrorPanel = ({ cluster, errorMessage }) => (
  React.createElement(WarningPanel, {
    title: "There was a problem formatting the link to the Kubernetes dashboard"          ,
    message: `Could not format the link to the dashboard of your cluster named '${
      cluster.name
    }'. Its dashboardApp property has been set to '${
      cluster.dashboardApp || 'standard'
    }.'`,}
  
    , errorMessage && (
      React.createElement(Typography, { variant: "body2",}, "Errors: " , errorMessage)
    )
  )
);












function replaceNullsWithUndefined(someObj) {
  const replacer = (_, value) =>
    String(value) === 'null' || String(value) === 'undefined'
      ? undefined
      : value;

  return JSON.parse(JSON.stringify(someObj, replacer));
}

function tryFormatClusterLink(options) {
  try {
    return {
      clusterLink: formatClusterLink(options),
      errorMessage: '',
    };
  } catch (err) {
    return {
      clusterLink: '',
      errorMessage: err.message || err.toString(),
    };
  }
}

const KubernetesDrawerContent = ({
  toggleDrawer,
  object,
  renderObject,
  kind,
}) => {
  const [isYaml, setIsYaml] = useState(false);

  const classes = useDrawerContentStyles();
  const cluster = useContext(ClusterContext);
  const { clusterLink, errorMessage } = tryFormatClusterLink({
    dashboardUrl: cluster.dashboardUrl,
    dashboardApp: cluster.dashboardApp,
    dashboardParameters: cluster.dashboardParameters,
    object,
    kind,
  });

  return (
    React.createElement(React.Fragment, null
      , React.createElement('div', { className: classes.header,}
        , React.createElement(Grid, {
          container: true,
          direction: "column",
          justifyContent: "flex-start",
          alignItems: "flex-start",}
        
          , React.createElement(Grid, { item: true,}
            , React.createElement(Typography, { variant: "h5",}
              , object.metadata?.name ?? 'unknown name'
            )
          )
          , React.createElement(Grid, { item: true,}
            , React.createElement(Typography, { color: "textSecondary", variant: "body1",}
              , kind
            )
          )
        )
        , React.createElement(IconButton, {
          key: "dismiss",
          title: "Close the drawer"  ,
          onClick: e => toggleDrawer(e, false),
          color: "inherit",}
        
          , React.createElement(Close, { className: classes.icon,} )
        )
      )
      , errorMessage && (
        React.createElement('div', { className: classes.errorMessage,}
          , React.createElement(ErrorPanel, { cluster: cluster, errorMessage: errorMessage,} )
        )
      )
      , React.createElement('div', { className: classes.options,}
        , React.createElement('div', null
          , clusterLink && (
            React.createElement(BackstageButton, {
              variant: "outlined",
              color: "primary",
              size: "small",
              to: clusterLink,
              endIcon: React.createElement(OpenInNewIcon, null ),}
            , "Open Kubernetes Dashboard"

            )
          )
        )
        , React.createElement(FormControlLabel, {
          control: 
            React.createElement(Switch, {
              checked: isYaml,
              onChange: event => {
                setIsYaml(event.target.checked);
              },
              name: "YAML",}
            )
          ,
          label: "YAML",}
        )
      )
      , React.createElement('div', { className: classes.content,}
        , isYaml && React.createElement(CodeSnippet, { language: "yaml", text: jsYaml.dump(object),} )
        , !isYaml && (
          React.createElement(StructuredMetadataTable, {
            metadata: renderObject(replaceNullsWithUndefined(object)),}
          )
        )
      )
    )
  );
};









export const KubernetesDrawer = ({
  object,
  renderObject,
  kind,
  buttonVariant = 'subtitle2',
  expanded = false,
  children,
}) => {
  const [isOpen, setIsOpen] = useState(expanded);
  const classes = useDrawerStyles();

  const toggleDrawer = (e, newValue) => {
    e.stopPropagation();
    setIsOpen(newValue);
  };

  return (
    React.createElement(React.Fragment, null
      , React.createElement(PodDrawerButton, {
        onClick: e => toggleDrawer(e, true),
        onFocus: event => event.stopPropagation(),}
      
        , children === undefined ? (
          React.createElement(Typography, { variant: buttonVariant,}
            , object.metadata?.name ?? 'unknown object'
          )
        ) : (
          children
        )
      )
      , React.createElement(Drawer, {
        classes: {
          paper: classes.paper,
        },
        anchor: "right",
        open: isOpen,
        onClose: (e) => toggleDrawer(e, false),
        onClick: event => event.stopPropagation(),}
      
        , React.createElement(KubernetesDrawerContent, {
          kind: kind,
          toggleDrawer: toggleDrawer,
          object: object,
          renderObject: renderObject,}
        )
      )
    )
  );
};
