
















import { DEFAULT_DURATION } from './duration';







export var DefaultLoadingAction; (function (DefaultLoadingAction) {
  const UserGroups = 'user-groups'; DefaultLoadingAction["UserGroups"] = UserGroups;
  const LastCompleteBillingDate = 'billing-date'; DefaultLoadingAction["LastCompleteBillingDate"] = LastCompleteBillingDate;
  const CostInsightsInitial = 'cost-insights-initial'; DefaultLoadingAction["CostInsightsInitial"] = CostInsightsInitial;
  const CostInsightsPage = 'cost-insights-page'; DefaultLoadingAction["CostInsightsPage"] = CostInsightsPage;
  const CostInsightsProducts = 'cost-insights-products'; DefaultLoadingAction["CostInsightsProducts"] = CostInsightsProducts;
  const CostInsightsAlerts = 'cost-insights-alerts'; DefaultLoadingAction["CostInsightsAlerts"] = CostInsightsAlerts;
})(DefaultLoadingAction || (DefaultLoadingAction = {}));

export const INITIAL_LOADING_ACTIONS = [
  DefaultLoadingAction.UserGroups,
  DefaultLoadingAction.CostInsightsInitial,
  DefaultLoadingAction.CostInsightsProducts,
];

export const getDefaultState = (loadingActions) => {
  return loadingActions.reduce(
    (defaultState, action) => ({ ...defaultState, [action]: true }),
    {},
  );
};

export const getResetState = (loadingActions) => {
  return loadingActions.reduce(
    (defaultState, action) => ({ ...defaultState, [action]: false }),
    {},
  );
};

export const getResetStateWithoutInitial = (
  loadingActions,
) => {
  return loadingActions.reduce((defaultState, action) => {
    const loadingActionState = (INITIAL_LOADING_ACTIONS ).includes(
      action,
    )
      ? false
      : true;
    return { ...defaultState, [action]: loadingActionState };
  }, {});
};

export const settledResponseOf = (
  responses,
) => {
  return responses.map(response =>
    response.status === 'fulfilled' ? response.value : null,
  );
};

export const initialStatesOf = (
  products,
  responses,
) => {
  return products.map((product, index) => ({
    entity: responses[index],
    product: product,
    duration: DEFAULT_DURATION,
  }));
};
