import React, { useEffect, useRef, useState } from 'react';
import {
  capitalize,
  Box,
  Button,
  Divider,
  Dialog,
  DialogActions,
  IconButton,
  DialogContent,
  Typography,
} from '@material-ui/core';
import { default as CloseIcon } from '@material-ui/icons/Close';
import { useAlertDialogStyles as useStyles } from '../../utils/styles';

import { choose, formOf } from '../../utils/alerts';

const DEFAULT_FORM_ID = 'alert-form';










export const AlertDialog = ({
  open,
  group,
  alert,
  status,
  onClose,
  onSubmit,
}) => {
  const classes = useStyles();
  const [isSubmitDisabled, setSubmitDisabled] = useState(true);
  const formRef = useRef(null);

  useEffect(() => {
    setSubmitDisabled(open);
  }, [open]);

  function disableSubmit(isDisabled) {
    setSubmitDisabled(isDisabled);
  }

  function onDialogClose() {
    onClose();
    setSubmitDisabled(true);
  }

  const [action, actioned] = choose(
    status,
    [
      ['snooze', 'snoozed'],
      ['accept', 'accepted'],
      ['dismiss', 'dismissed'],
    ],
    ['', ''],
  );

  const TransitionProps = {
    mountOnEnter: true,
    unmountOnExit: true,
    onEntered() {
      if (formRef.current) {
        formRef.current.id = DEFAULT_FORM_ID;
      }
    },
  };

  const Form = formOf(alert, status);

  return (
    React.createElement(Dialog, {
      open: open,
      onClose: onDialogClose,
      scroll: "body",
      maxWidth: "lg",
      TransitionProps: TransitionProps,}
    
      , React.createElement(Box, { display: "flex", justifyContent: "flex-end",}
        , React.createElement(IconButton, {
          className: classes.icon,
          disableRipple: true,
          'aria-label': "Close",
          onClick: onDialogClose,}
        
          , React.createElement(CloseIcon, null )
        )
      )
      , React.createElement(DialogContent, { className: classes.content,}
        , React.createElement(Box, { mb: 1.5,}
          , React.createElement(Typography, { variant: "h5",}
            , React.createElement('b', null, capitalize(action), " this action item?"   )
          )
          , React.createElement(Typography, { variant: "h6", color: "textSecondary",}
            , React.createElement('b', null, "This action item will be "
                   , actioned, " for all of "    , group, "."
            )
          )
        )
        , React.createElement(Box, {
          display: "flex",
          flexDirection: "column",
          bgcolor: "alertBackground",
          p: 2,
          mb: 1.5,
          borderRadius: 4,}
        
          , React.createElement(Typography, null
            , React.createElement('b', null, alert?.title)
          )
          , React.createElement(Typography, { color: "textSecondary",}, alert?.subtitle)
        )
        , Form && (
          React.createElement(Form, {
            ref: formRef,
            alert: alert,
            onSubmit: onSubmit,
            disableSubmit: disableSubmit,}
          )
        )
      )
      , React.createElement(Divider, null )
      , React.createElement(DialogActions, { className: classes.actions, disableSpacing: true,}
        , Form ? (
          React.createElement(Button, {
            type: "submit",
            color: "primary",
            variant: "contained",
            'aria-label': action,
            form: DEFAULT_FORM_ID,
            disabled: isSubmitDisabled,}
          
            , capitalize(action)
          )
        ) : (
          React.createElement(Button, {
            type: "button",
            color: "primary",
            variant: "contained",
            'aria-label': action,
            onClick: () => onSubmit(null),}
          
            , capitalize(action)
          )
        )
      )
    )
  );
};
