import { RELATION_CONSUMES_API } from '@backstage/catalog-model';
import { Typography } from '@material-ui/core';
import {
  EntityTable,
  useEntity,
  useRelatedEntities,
} from '@backstage/plugin-catalog-react';
import React from 'react';
import { apiEntityColumns } from './presets';
import {
  CodeSnippet,
  InfoCard,

  Link,
  Progress,
  WarningPanel,
} from '@backstage/core-components';





export const ConsumedApisCard = ({ variant = 'gridItem' }) => {
  const { entity } = useEntity();
  const { entities, loading, error } = useRelatedEntities(entity, {
    type: RELATION_CONSUMES_API,
  });

  if (loading) {
    return (
      React.createElement(InfoCard, { variant: variant, title: "Consumed APIs" ,}
        , React.createElement(Progress, null )
      )
    );
  }

  if (error || !entities) {
    return (
      React.createElement(InfoCard, { variant: variant, title: "Consumed APIs" ,}
        , React.createElement(WarningPanel, {
          severity: "error",
          title: "Could not load APIs"   ,
          message: React.createElement(CodeSnippet, { text: `${error}`, language: "text",} ),}
        )
      )
    );
  }

  return (
    React.createElement(EntityTable, {
      title: "Consumed APIs" ,
      variant: variant,
      emptyContent: 
        React.createElement('div', { style: { textAlign: 'center' },}
          , React.createElement(Typography, { variant: "body1",}, "This "
             , entity.kind.toLocaleLowerCase('en-US'), " does not consume any APIs."

          )
          , React.createElement(Typography, { variant: "body2",}
            , React.createElement(Link, { to: "https://backstage.io/docs/features/software-catalog/descriptor-format#specconsumesapis-optional",}, "Learn how to change this."

            )
          )
        )
      ,
      columns: apiEntityColumns,
      entities: entities ,}
    )
  );
};
