import React, {

  useEffect,
  useState,
  forwardRef,

} from 'react';
import {
  Box,
  Collapse,
  FormControl,
  FormControlLabel,
  TextField,
  Typography,
  Radio,
  RadioGroup,
} from '@material-ui/core';
import {


  AlertDismissReason,
  AlertDismissOptions,


} from '../types';
import { useAlertDialogStyles as useStyles } from '../utils/styles';



export const AlertDismissForm = forwardRef


(({ onSubmit, disableSubmit }, ref) => {
  const classes = useStyles();
  const [other, setOther] = useState(null);
  const [feedback, setFeedback] = useState(null);
  const [reason, setReason] = useState(
    AlertDismissReason.Resolved,
  );

  const onFormSubmit = e => {
    e.preventDefault();
    if (reason) {
      onSubmit({
        other: other,
        reason: reason,
        feedback: feedback,
      });
    }
  };

  const onReasonChange = (_, value) => {
    if (other) {
      setOther(null);
    }
    setReason(value );
  };

  const onOtherChange = (e) => {
    return e.target.value
      ? setOther(e.target.value )
      : setOther(null);
  };

  const onFeedbackChange = (e) => {
    return e.target.value
      ? setFeedback(e.target.value )
      : setFeedback(null);
  };

  useEffect(() => {
    function validateDismissForm() {
      if (reason === AlertDismissReason.Other) {
        if (other) {
          disableSubmit(false);
        } else {
          disableSubmit(true);
        }
      } else if (reason) {
        disableSubmit(false);
      } else {
        disableSubmit(true);
      }
    }

    validateDismissForm();
  }, [reason, other, disableSubmit]);

  return (
    React.createElement('form', { ref: ref, onSubmit: onFormSubmit,}
      , React.createElement(FormControl, { component: "fieldset", fullWidth: true,}
        , React.createElement(Typography, { color: "textPrimary",}
          , React.createElement('b', null, "Reason for dismissing?"  )
        )
        , React.createElement(Box, { mb: 1,}
          , React.createElement(RadioGroup, {
            name: "dismiss-alert-reasons",
            value: reason,
            onChange: onReasonChange,}
          
            , AlertDismissOptions.map(option => (
              React.createElement(FormControlLabel, {
                key: `dismiss-alert-option-${option.reason}`,
                label: option.label,
                value: option.reason,
                control: React.createElement(Radio, { className: classes.radio,} ),}
              )
            ))
          )
          , React.createElement(Collapse, { in: reason === AlertDismissReason.Other,}
            , React.createElement(Box, { ml: 4,}
              , React.createElement(TextField, {
                id: "dismiss-alert-option-other",
                variant: "outlined",
                multiline: true,
                fullWidth: true,
                rows: 4,
                value: other ?? '',
                onChange: onOtherChange,}
              )
            )
          )
        )
        , React.createElement(Typography, { gutterBottom: true,}
          , React.createElement('b', null, "Any other feedback you can provide?"     )
        )
        , React.createElement(TextField, {
          id: "dismiss-alert-feedback",
          variant: "outlined",
          multiline: true,
          rows: 4,
          fullWidth: true,
          value: feedback ?? '',
          onChange: onFeedbackChange,}
        )
      )
    )
  );
});
