import React from 'react';
import { Divider, Grid, Typography } from '@material-ui/core';

import { ErrorPanel } from './ErrorPanel';
import { ErrorReporting } from './ErrorReporting';
import { detectErrors } from '../error-detection';
import { Cluster } from './Cluster';
import EmptyStateImage from '../assets/emptystate.svg';
import { useKubernetesObjects } from '../hooks';
import { Content, Page, Progress } from '@backstage/core-components';







export const KubernetesContent = ({
  entity,
  refreshIntervalMs,
}) => {
  const { kubernetesObjects, error } = useKubernetesObjects(
    entity,
    refreshIntervalMs,
  );

  const clustersWithErrors =
    kubernetesObjects?.items.filter(r => r.errors.length > 0) ?? [];

  const detectedErrors =
    kubernetesObjects !== undefined
      ? detectErrors(kubernetesObjects)
      : new Map();

  return (
    React.createElement(Page, { themeId: "tool",}
      , React.createElement(Content, null
        , kubernetesObjects === undefined && error === undefined && React.createElement(Progress, null )

        /* errors retrieved from the kubernetes clusters */
        , clustersWithErrors.length > 0 && (
          React.createElement(Grid, { container: true, spacing: 3, direction: "column",}
            , React.createElement(Grid, { item: true,}
              , React.createElement(ErrorPanel, {
                entityName: entity.metadata.name,
                clustersWithErrors: clustersWithErrors,}
              )
            )
          )
        )

        /* other errors */
        , error !== undefined && (
          React.createElement(Grid, { container: true, spacing: 3, direction: "column",}
            , React.createElement(Grid, { item: true,}
              , React.createElement(ErrorPanel, {
                entityName: entity.metadata.name,
                errorMessage: error,}
              )
            )
          )
        )

        , kubernetesObjects && (
          React.createElement(Grid, { container: true, spacing: 3, direction: "column",}
            , React.createElement(Grid, { item: true,}
              , React.createElement(ErrorReporting, { detectedErrors: detectedErrors,} )
            )
            , React.createElement(Grid, { item: true,}
              , React.createElement(Divider, null )
            )
            , React.createElement(Grid, { item: true,}
              , React.createElement(Typography, { variant: "h3",}, "Your Clusters" )
            )
            , React.createElement(Grid, { item: true, container: true,}
              , kubernetesObjects?.items.length <= 0 && (
                React.createElement(Grid, {
                  container: true,
                  justifyContent: "space-around",
                  direction: "row",
                  alignItems: "center",
                  spacing: 2,}
                
                  , React.createElement(Grid, { item: true, xs: 4,}
                    , React.createElement(Typography, { variant: "h5",}, "No resources on any known clusters for"
                            , ' '
                      , entity.metadata.name
                    )
                  )
                  , React.createElement(Grid, { item: true, xs: 4,}
                    , React.createElement('img', {
                      src: EmptyStateImage,
                      alt: "EmptyState",
                      'data-testid': "emptyStateImg",}
                    )
                  )
                )
              )
              , kubernetesObjects?.items.length > 0 &&
                kubernetesObjects?.items.map((item, i) => {
                  const podsWithErrors = new Set(
                    detectedErrors
                      .get(item.cluster.name)
                      ?.filter(de => de.kind === 'Pod')
                      .map(de => de.names)
                      .flat() ?? [],
                  );

                  return (
                    React.createElement(Grid, { item: true, key: i, xs: 12,}
                      , React.createElement(Cluster, {
                        clusterObjects: item,
                        podsWithErrors: podsWithErrors,}
                      )
                    )
                  );
                })
            )
          )
        )
      )
    )
  );
};
