import React from 'react';
import { Link } from '@backstage/core-components';
import {
  ListItem,
  ListItemSecondaryAction,
  Tooltip,
  ListItemText,
  makeStyles,
  IconButton,
  Typography,
} from '@material-ui/core';
import { DateTime, Duration } from 'luxon';

import OpenInBrowserIcon from '@material-ui/icons/OpenInBrowser';


const useStyles = makeStyles({
  denseListIcon: {
    marginRight: 0,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  listItemPrimary: {
    fontWeight: 'bold',
  },
});





export const ChangeEventListItem = ({ changeEvent }) => {
  const classes = useStyles();
  const duration =
    new Date().getTime() - new Date(changeEvent.timestamp).getTime();
  const changedAt = DateTime.local()
    .minus(Duration.fromMillis(duration))
    .toRelative({ locale: 'en' });
  let externalLinkElem = null;
  if (changeEvent.links.length > 0) {
    const text = changeEvent.links[0].text;
    externalLinkElem = (
      React.createElement(Tooltip, { title: text, placement: "top",}
        , React.createElement(IconButton, {
          component: Link,
          to: changeEvent.links[0].href,
          color: "primary",}
        
          , React.createElement(OpenInBrowserIcon, null )
        )
      )
    );
  }

  return (
    React.createElement(ListItem, { dense: true, key: changeEvent.id,}
      , React.createElement(ListItemText, {
        primary: React.createElement(React.Fragment, null, changeEvent.summary),
        primaryTypographyProps: {
          variant: 'body1',
          className: classes.listItemPrimary,
        },
        secondary: 
          React.createElement(Typography, { variant: "body2", color: "textSecondary",}, "Triggered from "
              , changeEvent.source, " " , changedAt, "."
          )
        ,}
      )
      , React.createElement(ListItemSecondaryAction, null
        , externalLinkElem
        , changeEvent.html_url === undefined ? null : (
          React.createElement(Tooltip, { title: "View in PagerDuty"  , placement: "top",}
            , React.createElement(IconButton, {
              component: Link,
              to: changeEvent.html_url,
              color: "primary",}
            
              , React.createElement(OpenInBrowserIcon, null )
            )
          )
        )
      )
    )
  );
};
