
















import React, { useEffect } from 'react';
import useAsync from 'react-use/lib/useAsync';
import { sentryApiRef } from '../../api';
import SentryIssuesTable from '../SentryIssuesTable/SentryIssuesTable';
import {
  SENTRY_PROJECT_SLUG_ANNOTATION,
  useProjectSlug,
} from '../useProjectSlug';

import {
  EmptyState,
  InfoCard,

  MissingAnnotationEmptyState,
  Progress,
} from '@backstage/core-components';

import { errorApiRef, useApi } from '@backstage/core-plugin-api';


export const SentryIssuesWidget = ({
  entity,
  statsFor,
  tableOptions,
  variant = 'gridItem',
  query = '',
}





) => {
  const errorApi = useApi(errorApiRef);
  const sentryApi = useApi(sentryApiRef);

  const projectId = useProjectSlug(entity);

  const { loading, value, error } = useAsync(
    () => sentryApi.fetchIssues(projectId, statsFor, query),
    [sentryApi, statsFor, projectId, query],
  );

  useEffect(() => {
    if (error) {
      errorApi.post(error);
    }
  }, [error, errorApi]);

  if (loading || !projectId || error) {
    return (
      React.createElement(InfoCard, { title: "Sentry issues" , variant: variant,}
        , loading && React.createElement(Progress, null )

        , !loading && !projectId && (
          React.createElement(MissingAnnotationEmptyState, {
            annotation: SENTRY_PROJECT_SLUG_ANNOTATION,}
          )
        )

        , !loading && error && (
          React.createElement(EmptyState, {
            missing: "info",
            title: "No information to display"   ,
            description: `There is no Sentry project with id '${projectId}'.`,}
          )
        )
      )
    );
  }

  return (
    React.createElement(SentryIssuesTable, {
      sentryIssues: value || [],
      statsFor: statsFor,
      tableOptions: tableOptions,}
    )
  );
};
