import { CircularProgress, TextField } from '@material-ui/core';

import { Autocomplete } from '@material-ui/lab';
import React from 'react';
import { Controller, } from 'react-hook-form';

/**
 * Props for {@link AutocompleteTextField}.
 *
 * @public
 */

















/**
 * An autocompletion text field for the catalog import flows.
 *
 * @public
 */
export const AutocompleteTextField = (
  props,
) => {
  const {
    name,
    options,
    required,
    errors,
    rules,
    loading = false,
    loadingText,
    helperText,
    errorHelperText,
    textFieldProps = {},
  } = props;

  return (
    React.createElement(Controller, {
      name: name,
      rules: rules,
      render: ({ field: { onChange } }) => (
        React.createElement(Autocomplete, {
          loading: loading,
          loadingText: loadingText,
          options: options || [],
          autoSelect: true,
          freeSolo: true,
          onChange: (_event, value) =>
            onChange(value)
          ,
          renderInput: params => (
            React.createElement(TextField, {
              ...params,
              helperText: (errors?.[name] && errorHelperText) || helperText,
              error: Boolean(errors?.[name]),
              margin: "normal",
              variant: "outlined",
              required: required,
              InputProps: {
                ...params.InputProps,
                endAdornment: (
                  React.createElement(React.Fragment, null
                    , loading ? (
                      React.createElement(CircularProgress, { color: "inherit", size: "1em",} )
                    ) : null
                    , params.InputProps.endAdornment
                  )
                ),
              },
              ...textFieldProps,}
            )
          ),}
        )
      ),}
    )
  );
};
