import React, {


  useState,
  useEffect,
  useCallback,
} from 'react';
import useDebounce from 'react-use/lib/useDebounce';
import { configApiRef, useApi } from '@backstage/core-plugin-api';
import {
  InputBase,

  InputAdornment,
  IconButton,
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import ClearButton from '@material-ui/icons/Clear';

import {
  SearchContextProvider,
  useSearch,
  useSearchContextCheck,
} from '@backstage/plugin-search-react';
import { TrackSearch } from '../SearchTracker';

/**
 * Props for {@link SearchBarBase}.
 *
 * @public
 */








/**
 * All search boxes exported by the search plugin are based on the <SearchBarBase />,
 * and this one is based on the <InputBase /> component from Material UI.
 * Recommended if you don't use Search Provider or Search Context.
 *
 * @public
 */
export const SearchBarBase = ({
  onChange,
  onKeyDown,
  onSubmit,
  debounceTime = 200,
  clearButton = true,
  fullWidth = true,
  value: defaultValue,
  inputProps: defaultInputProps = {},
  endAdornment: defaultEndAdornment,
  ...props
}) => {
  const configApi = useApi(configApiRef);
  const [value, setValue] = useState(defaultValue );
  const hasSearchContext = useSearchContextCheck();

  useEffect(() => {
    setValue(prevValue =>
      prevValue !== defaultValue ? (defaultValue ) : prevValue,
    );
  }, [defaultValue]);

  useDebounce(() => onChange(value), debounceTime, [value]);

  const handleChange = useCallback(
    (e) => {
      setValue(e.target.value);
    },
    [setValue],
  );

  const handleKeyDown = useCallback(
    (e) => {
      if (onKeyDown) onKeyDown(e);
      if (onSubmit && e.key === 'Enter') {
        onSubmit();
      }
    },
    [onKeyDown, onSubmit],
  );

  const handleClear = useCallback(() => {
    onChange('');
  }, [onChange]);

  const placeholder = `Search in ${
    configApi.getOptionalString('app.title') || 'Backstage'
  }`;

  const startAdornment = (
    React.createElement(InputAdornment, { position: "start",}
      , React.createElement(IconButton, { 'aria-label': "Query", disabled: true,}
        , React.createElement(SearchIcon, null )
      )
    )
  );

  const endAdornment = (
    React.createElement(InputAdornment, { position: "end",}
      , React.createElement(IconButton, { 'aria-label': "Clear", onClick: handleClear,}
        , React.createElement(ClearButton, null )
      )
    )
  );

  const searchBar = (
    React.createElement(TrackSearch, null
      , React.createElement(InputBase, {
        'data-testid': "search-bar-next",
        value: value,
        placeholder: placeholder,
        startAdornment: startAdornment,
        endAdornment: clearButton ? endAdornment : defaultEndAdornment,
        inputProps: { 'aria-label': 'Search', ...defaultInputProps },
        fullWidth: fullWidth,
        onChange: handleChange,
        onKeyDown: handleKeyDown,
        ...props,}
      )
    )
  );

  return hasSearchContext ? (
    searchBar
  ) : (
    React.createElement(SearchContextProvider, null, searchBar)
  );
};

/**
 * Props for {@link SearchBar}.
 *
 * @public
 */


/**
 * Recommended search bar when you use the Search Provider or Search Context.
 *
 * @public
 */
export const SearchBar = ({ onChange, ...props }) => {
  const { term, setTerm } = useSearch();

  const handleChange = useCallback(
    (newValue) => {
      if (onChange) {
        onChange(newValue);
      } else {
        setTerm(newValue);
      }
    },
    [onChange, setTerm],
  );

  return React.createElement(SearchBarBase, { value: term, onChange: handleChange, ...props,} );
};
