import { Divider, Grid, makeStyles, Typography } from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import React, { useEffect, useState } from 'react';
import useAsync from 'react-use/lib/useAsync';
import { catalogApiRef } from '@backstage/plugin-catalog-react';

import { Filters, FiltersButton, } from './Filters';
import { DEFAULT_NAMESPACE } from '@backstage/catalog-model';

import {
  EmptyState,
  Link,
  Progress,
  Table,

} from '@backstage/core-components';
import { useApi } from '@backstage/core-plugin-api';











const useStyles = makeStyles(theme => ({
  searchQuery: {
    color: theme.palette.text.primary,
    background: theme.palette.background.default,
    borderRadius: '10%',
  },
  tableHeader: {
    margin: theme.spacing(1, 0, 0, 0),
    display: 'flex',
  },
  divider: {
    width: '1px',
    margin: theme.spacing(0, 2),
    padding: theme.spacing(2, 0),
  },
}));












// TODO: move out column to make the search result component more generic
const columns = [
  {
    title: 'Name',
    field: 'name',
    highlight: true,
    render: (result) => (
      React.createElement(Link, { to: result.url || '',}, result.name)
    ),
  },
  {
    title: 'Description',
    field: 'description',
  },
  {
    title: 'Owner',
    field: 'owner',
  },
  {
    title: 'Kind',
    field: 'kind',
  },
  {
    title: 'LifeCycle',
    field: 'lifecycle',
  },
];

const TableHeader = ({
  searchQuery,
  numberOfSelectedFilters,
  numberOfResults,
  handleToggleFilters,
}) => {
  const classes = useStyles();

  return (
    React.createElement('div', { className: classes.tableHeader,}
      , React.createElement(FiltersButton, {
        numberOfSelectedFilters: numberOfSelectedFilters,
        handleToggleFilters: handleToggleFilters,}
      )
      , React.createElement(Divider, { className: classes.divider, orientation: "vertical",} )
      , React.createElement(Grid, { item: true, xs: 12,}
        , searchQuery ? (
          React.createElement(Typography, { variant: "h6",}
            , `${numberOfResults} `
            , numberOfResults > 1 ? `results for ` : `result for `
            , React.createElement('span', { className: classes.searchQuery,}, "\"", searchQuery, "\""), ' '
          )
        ) : (
          React.createElement(Typography, { variant: "h6",}, `${numberOfResults} results`)
        )
      )
    )
  );
};

export const SearchResult = ({ searchQuery }) => {
  const catalogApi = useApi(catalogApiRef);

  const [showFilters, toggleFilters] = useState(false);
  const [selectedFilters, setSelectedFilters] = useState({
    selected: '',
    checked: [],
  });

  const [filteredResults, setFilteredResults] = useState([]);

  const {
    loading,
    error,
    value: results,
  } = useAsync(async () => {
    const entities = await catalogApi.getEntities();
    return entities.items.map((entity) => ({
      name: entity.metadata.name,
      description: entity.metadata.description,
      owner:
        typeof entity.spec?.owner === 'string' ? entity.spec?.owner : undefined,
      kind: entity.kind,
      lifecycle:
        typeof entity.spec?.lifecycle === 'string'
          ? entity.spec?.lifecycle
          : undefined,
      url: `/catalog/${
        entity.metadata.namespace?.toLocaleLowerCase('en-US') ||
        DEFAULT_NAMESPACE
      }/${entity.kind.toLocaleLowerCase('en-US')}/${entity.metadata.name}`,
    }));
  }, []);

  useEffect(() => {
    if (results) {
      let withFilters = results;

      // apply filters

      // filter on selected
      if (selectedFilters.selected !== '') {
        withFilters = results.filter((result) =>
          selectedFilters.selected.includes(result.kind),
        );
      }

      // filter on checked
      if (selectedFilters.checked.length > 0) {
        withFilters = withFilters.filter(
          (result) =>
            result.lifecycle &&
            selectedFilters.checked.includes(result.lifecycle),
        );
      }

      // filter on searchQuery
      if (searchQuery) {
        withFilters = withFilters.filter(
          (result) =>
            result.name?.toLocaleLowerCase('en-US').includes(searchQuery) ||
            result.name
              ?.toLocaleLowerCase('en-US')
              .includes(searchQuery.split(' ').join('-')) ||
            result.description
              ?.toLocaleLowerCase('en-US')
              .includes(searchQuery),
        );
      }

      setFilteredResults(withFilters);
    }
  }, [selectedFilters, searchQuery, results]);
  if (loading) {
    return React.createElement(Progress, null );
  }
  if (error) {
    return (
      React.createElement(Alert, { severity: "error",}, "Error encountered while fetching search results. "
              , error.toString()
      )
    );
  }
  if (!results || results.length === 0) {
    return React.createElement(EmptyState, { missing: "data", title: "Sorry, no results were found"    ,} );
  }

  const resetFilters = () => {
    setSelectedFilters({
      selected: '',
      checked: [],
    });
  };

  const updateSelected = (filter) => {
    setSelectedFilters(prevState => ({
      ...prevState,
      selected: filter,
    }));
  };

  const updateChecked = (filter) => {
    if (selectedFilters.checked.includes(filter)) {
      setSelectedFilters(prevState => ({
        ...prevState,
        checked: prevState.checked.filter(item => item !== filter),
      }));
      return;
    }

    setSelectedFilters(prevState => ({
      ...prevState,
      checked: [...prevState.checked, filter],
    }));
  };

  const filterOptions = results.reduce(
    (acc, curr) => {
      if (curr.kind && acc.kind.indexOf(curr.kind) < 0) {
        acc.kind.push(curr.kind);
      }
      if (curr.lifecycle && acc.lifecycle.indexOf(curr.lifecycle) < 0) {
        acc.lifecycle.push(curr.lifecycle);
      }
      return acc;
    },
    {
      kind: [] ,
      lifecycle: [] ,
    },
  );

  return (
    React.createElement(React.Fragment, null
      , React.createElement(Grid, { container: true,}
        , showFilters && (
          React.createElement(Grid, { item: true, xs: 3,}
            , React.createElement(Filters, {
              filters: selectedFilters,
              filterOptions: filterOptions,
              resetFilters: resetFilters,
              updateSelected: updateSelected,
              updateChecked: updateChecked,}
            )
          )
        )
        , React.createElement(Grid, { item: true, xs: showFilters ? 9 : 12,}
          , React.createElement(Table, {
            options: { paging: true, pageSize: 20, search: false },
            data: filteredResults,
            columns: columns,
            title: 
              React.createElement(TableHeader, {
                searchQuery: searchQuery,
                numberOfResults: filteredResults.length,
                numberOfSelectedFilters: 
                  (selectedFilters.selected !== '' ? 1 : 0) +
                  selectedFilters.checked.length
                ,
                handleToggleFilters: () => toggleFilters(!showFilters),}
              )
            ,}
          )
        )
      )
    )
  );
};
