import { readGitHubIntegrationConfigs } from '@backstage/integration';
import { useEntity } from '@backstage/plugin-catalog-react';
import {
  LinearProgress,
  makeStyles,

  Typography,
} from '@material-ui/core';
import ExternalLinkIcon from '@material-ui/icons/Launch';
import React, { useEffect } from 'react';
import { GITHUB_ACTIONS_ANNOTATION } from '../getProjectNameFromEntity';
import { useWorkflowRuns, } from '../useWorkflowRuns';
import { WorkflowRunsTable } from '../WorkflowRunsTable';
import { WorkflowRunStatus } from '../WorkflowRunStatus';

import { configApiRef, errorApiRef, useApi } from '@backstage/core-plugin-api';
import {
  InfoCard,

  Link,
  StructuredMetadataTable,
} from '@backstage/core-components';

const useStyles = makeStyles({
  externalLinkIcon: {
    fontSize: 'inherit',
    verticalAlign: 'bottom',
  },
});

const WidgetContent = ({
  error,
  loading,
  lastRun,
  branch,
}




) => {
  const classes = useStyles();
  if (error) return React.createElement(Typography, null, "Couldn't fetch latest "   , branch, " run" );
  if (loading) return React.createElement(LinearProgress, null );
  return (
    React.createElement(StructuredMetadataTable, {
      metadata: {
        status: (
          React.createElement(React.Fragment, null
            , React.createElement(WorkflowRunStatus, {
              status: lastRun.status,
              conclusion: lastRun.conclusion,}
            )
          )
        ),
        message: lastRun.message,
        url: (
          React.createElement(Link, { to: lastRun.githubUrl ?? '',}, "See more on GitHub"
               , ' '
            , React.createElement(ExternalLinkIcon, { className: classes.externalLinkIcon,} )
          )
        ),
      },}
    )
  );
};

export const LatestWorkflowRunCard = ({
  branch = 'master',
  // Display the card full height suitable for
  variant,
}) => {
  const { entity } = useEntity();
  const config = useApi(configApiRef);
  const errorApi = useApi(errorApiRef);
  // TODO: Get github hostname from metadata annotation
  const hostname = readGitHubIntegrationConfigs(
    config.getOptionalConfigArray('integrations.github') ?? [],
  )[0].host;
  const [owner, repo] = (
    entity?.metadata.annotations?.[GITHUB_ACTIONS_ANNOTATION] ?? '/'
  ).split('/');
  const [{ runs, loading, error }] = useWorkflowRuns({
    hostname,
    owner,
    repo,
    branch,
  });
  const lastRun = runs?.[0] ?? ({} );
  useEffect(() => {
    if (error) {
      errorApi.post(error);
    }
  }, [error, errorApi]);

  return (
    React.createElement(InfoCard, { title: `Last ${branch} build`, variant: variant,}
      , React.createElement(WidgetContent, {
        error: error,
        loading: loading,
        branch: branch,
        lastRun: lastRun,}
      )
    )
  );
};






export const LatestWorkflowsForBranchCard = ({
  branch = 'master',
  variant,
}) => {
  const { entity } = useEntity();

  return (
    React.createElement(InfoCard, { title: `Last ${branch} build`, variant: variant,}
      , React.createElement(WorkflowRunsTable, { branch: branch, entity: entity,} )
    )
  );
};
