





























import { ResponseError } from '@backstage/errors';

export class AzureDevOpsClient  {
    discoveryApi;
    identityApi;

   constructor(options


) {
    this.discoveryApi = options.discoveryApi;
    this.identityApi = options.identityApi;
  }

   async getRepoBuilds(
    projectName,
    repoName,
    options,
  ) {
    const queryString = new URLSearchParams();
    if (options?.top) {
      queryString.append('top', options.top.toString());
    }
    const urlSegment = `repo-builds/${encodeURIComponent(
      projectName,
    )}/${encodeURIComponent(repoName)}?${queryString}`;

    const items = await this.get(urlSegment);
    return { items };
  }

   async getGitTags(
    projectName,
    repoName,
  ) {
    const urlSegment = `git-tags/${encodeURIComponent(
      projectName,
    )}/${encodeURIComponent(repoName)}`;

    const items = await this.get(urlSegment);
    return { items };
  }

   async getPullRequests(
    projectName,
    repoName,
    options,
  ) {
    const queryString = new URLSearchParams();
    if (options?.top) {
      queryString.append('top', options.top.toString());
    }
    if (options?.status) {
      queryString.append('status', options.status.toString());
    }
    const urlSegment = `pull-requests/${encodeURIComponent(
      projectName,
    )}/${encodeURIComponent(repoName)}?${queryString}`;

    const items = await this.get(urlSegment);
    return { items };
  }

   getDashboardPullRequests(
    projectName,
  ) {
    return this.get(
      `dashboard-pull-requests/${projectName}?top=100`,
    );
  }

   getAllTeams() {
    return this.get('all-teams');
  }

   getUserTeamIds(userId) {
    return this.get(`users/${userId}/team-ids`);
  }

   async getBuildRuns(
    projectName,
    repoName,
    definitionName,
    options,
  ) {
    const queryString = new URLSearchParams();
    if (repoName) {
      queryString.append('repoName', repoName);
    }
    if (definitionName) {
      queryString.append('definitionName', definitionName);
    }
    if (options?.top) {
      queryString.append('top', options.top.toString());
    }
    const urlSegment = `builds/${encodeURIComponent(
      projectName,
    )}?${queryString}`;
    const items = await this.get(urlSegment);
    return { items };
  }

   async get(path) {
    const baseUrl = `${await this.discoveryApi.getBaseUrl('azure-devops')}/`;
    const url = new URL(path, baseUrl);

    const { token: idToken } = await this.identityApi.getCredentials();
    const response = await fetch(url.toString(), {
      headers: idToken ? { Authorization: `Bearer ${idToken}` } : {},
    });

    if (!response.ok) {
      throw await ResponseError.fromResponse(response);
    }

    return response.json() ;
  }
}
