import React from 'react';

import { useEntity } from '@backstage/plugin-catalog-react';
import { CodeCoveragePage } from './CodeCoveragePage';
import { MissingAnnotationEmptyState } from '@backstage/core-components';

export const isCodeCoverageAvailable = (entity) =>
  Boolean(entity.metadata.annotations?.['backstage.io/code-coverage']);

export const Router = () => {
  const { entity } = useEntity();

  if (!isCodeCoverageAvailable(entity)) {
    return (
      React.createElement(MissingAnnotationEmptyState, { annotation: "backstage.io/code-coverage",} )
    );
  }
  return React.createElement(CodeCoveragePage, null );
};
