import { RELATION_HAS_PART } from '@backstage/catalog-model';
import { Typography } from '@material-ui/core';
import {
  EntityTable,
  useEntity,
  useRelatedEntities,
} from '@backstage/plugin-catalog-react';
import React from 'react';
import { createSpecApiTypeColumn } from './presets';
import {
  CodeSnippet,
  InfoCard,

  Link,
  Progress,

  WarningPanel,
} from '@backstage/core-components';





const columns = [
  EntityTable.columns.createEntityRefColumn({ defaultKind: 'API' }),
  EntityTable.columns.createOwnerColumn(),
  createSpecApiTypeColumn(),
  EntityTable.columns.createSpecLifecycleColumn(),
  EntityTable.columns.createMetadataDescriptionColumn(),
];

export const HasApisCard = ({ variant = 'gridItem' }) => {
  const { entity } = useEntity();
  const { entities, loading, error } = useRelatedEntities(entity, {
    type: RELATION_HAS_PART,
    kind: 'API',
  });

  if (loading) {
    return (
      React.createElement(InfoCard, { variant: variant, title: "APIs",}
        , React.createElement(Progress, null )
      )
    );
  }

  if (error || !entities) {
    return (
      React.createElement(InfoCard, { variant: variant, title: "APIs",}
        , React.createElement(WarningPanel, {
          severity: "error",
          title: "Could not load APIs"   ,
          message: React.createElement(CodeSnippet, { text: `${error}`, language: "text",} ),}
        )
      )
    );
  }

  return (
    React.createElement(EntityTable, {
      title: "APIs",
      variant: variant,
      emptyContent: 
        React.createElement('div', { style: { textAlign: 'center' },}
          , React.createElement(Typography, { variant: "body1",}, "This "
             , entity.kind.toLocaleLowerCase('en-US'), " does not contain any APIs."

          )
          , React.createElement(Typography, { variant: "body2",}
            , React.createElement(Link, { to: "https://backstage.io/docs/features/software-catalog/descriptor-format#kind-api",}, "Learn how to change this."

            )
          )
        )
      ,
      columns: columns,
      entities: entities ,}
    )
  );
};
