import React from 'react';
import { Typography } from '@material-ui/core';

import { WarningPanel } from '@backstage/core-components';

const clustersWithErrorsToErrorMessage = (
  clustersWithErrors,
) => {
  return clustersWithErrors.map((c, i) => {
    return (
      React.createElement('div', { key: i,}
        , React.createElement(Typography, { variant: "body2",}, `Cluster: ${c.cluster.name}`)
        , c.errors.map((e, j) => {
          return (
            React.createElement(Typography, { variant: "body2", key: j,}
              , `Error fetching Kubernetes resource: '${e.resourcePath}', error: ${e.errorType}, status code: ${e.statusCode}`
            )
          );
        })
        , React.createElement('br', null )
      )
    );
  });
};








export const ErrorPanel = ({
  entityName,
  errorMessage,
  clustersWithErrors,
}) => (
  React.createElement(WarningPanel, {
    title: "There was a problem retrieving Kubernetes objects"      ,
    message: `There was a problem retrieving some Kubernetes resources for the entity: ${entityName}. This could mean that the Error Reporting card is not completely accurate.`,}
  
    , clustersWithErrors && (
      React.createElement('div', null, "Errors: " , clustersWithErrorsToErrorMessage(clustersWithErrors))
    )
    , errorMessage && (
      React.createElement(Typography, { variant: "body2",}, "Errors: " , errorMessage)
    )
  )
);
