import React from 'react';
import { Box, Grid } from '@material-ui/core';
import { useApi } from '@backstage/core-plugin-api';
import useAsync from 'react-use/lib/useAsync';
import {
  InfoCard,
  Progress,
  ErrorPanel,
  Link,
} from '@backstage/core-components';
import { newRelicDashboardApiRef } from '../../../api';









export const DashboardSnapshot = ({
  guid,
  name,
  permalink,
  duration,
}) => {
  const newRelicDashboardAPI = useApi(newRelicDashboardApiRef);
  const { value, loading, error } = useAsync(async (

) => {
    const dashboardObject =
      newRelicDashboardAPI.getDashboardSnapshot(guid, duration);
    return dashboardObject;
  }, [guid, duration]);
  if (loading) {
    return React.createElement(Progress, null );
  }
  if (error) {
    return React.createElement(ErrorPanel, { title: error.name, defaultExpanded: true, error: error,} );
  }
  const url =
    value?.getDashboardSnapshot?.data?.dashboardCreateSnapshotUrl?.replace(
      /\pdf$/i,
      'png',
    );
  return (
    React.createElement(Grid, { container: true, style: { marginTop: '30px' },}
      , React.createElement(InfoCard, { variant: "gridItem", title: name,}
        , React.createElement(Box, { display: "flex",}
          , React.createElement(Box, { flexGrow: "1",}
            , React.createElement(Link, { to: permalink,}
              , url ? (
                React.createElement('img', {
                  alt: `${name} Dashbord`,
                  style: { border: 'solid 1px black' },
                  src: url,}
                )
              ) : (
                'Dashboard loading... , click here to open if it did not render correctly'
              )
            )
          )
        )
      )
    )
  );
};
