export var GoCdBuildResultStatus; (function (GoCdBuildResultStatus) {
  const running = 0; GoCdBuildResultStatus[GoCdBuildResultStatus["running"] = running] = "running";
  const successful = running + 1; GoCdBuildResultStatus[GoCdBuildResultStatus["successful"] = successful] = "successful";
  const warning = successful + 1; GoCdBuildResultStatus[GoCdBuildResultStatus["warning"] = warning] = "warning";
  const aborted = warning + 1; GoCdBuildResultStatus[GoCdBuildResultStatus["aborted"] = aborted] = "aborted";
  const error = aborted + 1; GoCdBuildResultStatus[GoCdBuildResultStatus["error"] = error] = "error";
  const pending = error + 1; GoCdBuildResultStatus[GoCdBuildResultStatus["pending"] = pending] = "pending";
})(GoCdBuildResultStatus || (GoCdBuildResultStatus = {}));

export const toBuildResultStatus = (status) => {
  switch (status.toLocaleLowerCase('en-US')) {
    case 'passed':
      return GoCdBuildResultStatus.successful;
    case 'failed':
      return GoCdBuildResultStatus.error;
    case 'aborted':
      return GoCdBuildResultStatus.aborted;
    case 'building':
      return GoCdBuildResultStatus.running;
    case 'pending':
      return GoCdBuildResultStatus.pending;
    default:
      return GoCdBuildResultStatus.aborted;
  }
};

















