import React from 'react';
import { InputLabel, FormControl, Select, MenuItem } from '@material-ui/core';

import { useSelectStyles as useStyles } from '../../utils/styles';







export const MetricSelect = ({
  metric,
  metrics,
  onSelect,
}) => {
  const classes = useStyles();

  function onChange(e) {
    if (e.target.value === 'none') {
      onSelect(null);
    } else {
      onSelect(e.target.value );
    }
  }

  return (
    React.createElement(FormControl, { variant: "outlined",}
      , React.createElement(InputLabel, { shrink: true, id: "metric-select-label",}, "Compare to:"

      )
      , React.createElement(Select, {
        id: "metric-select",
        labelId: "metric-select-label",
        labelWidth: 100,
        className: classes.select,
        value: metric ?? 'none',
        onChange: onChange,}
      
        , React.createElement(MenuItem, { className: classes.menuItem, key: "none", value: "none",}
          , React.createElement('em', null, "None")
        )
        , metrics.map((m) => (
          React.createElement(MenuItem, { className: classes.menuItem, key: m.kind, value: m.kind,}
            , React.createElement('b', null, m.name)
          )
        ))
      )
    )
  );
};
