import React, { useEffect, useState } from 'react';
import {
  Collapse,
  MenuList,
  MenuItem,
  ListItemIcon,
  ListItemText,
  Typography,
  Badge,
} from '@material-ui/core';
import { useNavigationStyles as useStyles } from '../../utils/styles';
import { useConfig, useScroll } from '../../hooks';
import { findAlways } from '../../utils/assert';
import {
  DefaultNavigation,

  getDefaultNavigationItems,
} from '../../utils/navigation';







const NavigationMenuItem = ({ navigation, icon, title }) => {
  const classes = useStyles();
  const [, setScroll] = useScroll();
  return (
    React.createElement(MenuItem, {
      button: true,
      'data-testid': `menu-item-${navigation}`,
      className: classes.menuItem,
      onClick: () => setScroll(navigation),}
    
      , React.createElement(ListItemIcon, { className: classes.listItemIcon,}, icon)
      , React.createElement(ListItemText, {
        primary: React.createElement(Typography, { className: classes.title,}, title),}
      )
    )
  );
};

export const CostInsightsNavigation = React.memo(
  ({ alerts, products }) => {
    const classes = useStyles();
    const { icons } = useConfig();
    const [isOpen, setOpen] = useState(false);

    const defaultNavigationItems = getDefaultNavigationItems(alerts);
    const productNavigationItems =
      products?.map(product => ({
        title: product.name,
        navigation: product.kind,
        icon: findAlways(icons, i => i.kind === product.kind).component,
      })) ?? [];

    useEffect(
      function toggleProductMenuItems() {
        if (products?.length) {
          setOpen(true);
        } else {
          setOpen(false);
        }
      },
      [products],
    );

    return (
      React.createElement(MenuList, { className: classes.menuList,}
        , defaultNavigationItems.map(item => (
          React.createElement(NavigationMenuItem, {
            key: `navigation-menu-item-${item.navigation}`,
            navigation: item.navigation,
            title: item.title,
            icon: 
              item.navigation === DefaultNavigation.AlertInsightsHeader ? (
                React.createElement(Badge, { badgeContent: alerts, color: "secondary",}
                  , React.cloneElement(item.icon, {
                    className: classes.navigationIcon,
                  })
                )
              ) : (
                React.cloneElement(item.icon, {
                  className: classes.navigationIcon,
                })
              )
            ,}
          )
        ))
        , React.createElement(Collapse, { in: isOpen, timeout: 850,}
          , productNavigationItems.map((item) => (
            React.createElement(NavigationMenuItem, {
              key: `navigation-menu-item-${item.navigation}`,
              navigation: item.navigation,
              icon: React.cloneElement(item.icon, {
                className: classes.navigationIcon,
              }),
              title: item.title,}
            )
          ))
        )
      )
    );
  },
);
