export const isCircleCIAvailable = (entity) =>
  Boolean(entity.metadata.annotations?.[CIRCLECI_ANNOTATION]);

export const Router = () => {
  const { entity } = useEntity();

  if (!isCircleCIAvailable(entity)) {
    return React.createElement(MissingAnnotationEmptyState, { annotation: CIRCLECI_ANNOTATION,} );
  }

  return (
    React.createElement(Routes, null
      , React.createElement(Route, { path: "/", element: React.createElement(BuildsPage, null ),} )
      , React.createElement(Route, {
        path: `${circleCIBuildRouteRef.path}`,
        element: React.createElement(BuildWithStepsPage, null ),}
      )
    )
  );
};
