export const Cluster = ({ clusterObjects, podsWithErrors }) => {
  const groupedResponses = groupResponses(clusterObjects.resources);
  const podNameToMetrics = clusterObjects.podMetrics
    .flat()
    .reduce((accum, next) => {
      const name = next.pod.metadata?.name;
      if (name !== undefined) {
        accum.set(name, next);
      }
      return accum;
    }, new Map());
  return (
    React.createElement(ClusterContext.Provider, { value: clusterObjects.cluster,}
      , React.createElement(GroupedResponsesContext.Provider, { value: groupedResponses,}
        , React.createElement(PodNamesWithMetricsContext.Provider, { value: podNameToMetrics,}
          , React.createElement(PodNamesWithErrorsContext.Provider, { value: podsWithErrors,}
            , React.createElement(Accordion, { TransitionProps: { unmountOnExit: true },}
              , React.createElement(AccordionSummary, { expandIcon: React.createElement(ExpandMoreIcon, null ),}
                , React.createElement(ClusterSummary, {
                  clusterName: clusterObjects.cluster.name,
                  totalNumberOfPods: groupedResponses.pods.length,
                  numberOfPodsWithErrors: podsWithErrors.size,}
                )
              )
              , React.createElement(AccordionDetails, null
                , React.createElement(Grid, { container: true, direction: "column",}
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(CustomResources, null )
                  )
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(DeploymentsAccordions, null )
                  )
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(StatefulSetsAccordions, null )
                  )
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(IngressesAccordions, null )
                  )
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(ServicesAccordions, null )
                  )
                  , React.createElement(Grid, { item: true,}
                    , React.createElement(CronJobsAccordions, null )
                  )
                )
              )
            )
          )
        )
      )
    )
  );
};
