import React, { useContext } from 'react';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  Grid,
  Typography,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { PodsTable } from '../../Pods';
import { HorizontalPodAutoscalerDrawer } from '../../HorizontalPodAutoscalers';
import { RolloutDrawer } from './RolloutDrawer';
import PauseIcon from '@material-ui/icons/Pause';
import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
import { DateTime } from 'luxon';
import { StepsProgress } from './StepsProgress';
import {
  PodNamesWithErrorsContext,
  GroupedResponsesContext,
} from '../../../hooks';
import {
  getMatchingHpa,
  getOwnedPodsThroughReplicaSets,
} from '../../../utils/owner';
import { StatusError, StatusOK } from '@backstage/core-components';
import { READY_COLUMNS, RESOURCE_COLUMNS } from '../../Pods/PodsTable';























const AbortedTitle = (
  React.createElement('div', {
    style: {
      display: 'flex',
      alignItems: 'center',
      flexWrap: 'wrap',
    },}
  
    , React.createElement(ErrorOutlineIcon, null )
    , React.createElement(Typography, { variant: "subtitle1",}, "Aborted")
  )
);

const findAbortedMessage = (rollout) =>
  rollout.status?.conditions?.find(
    (c) =>
      c.type === 'Progressing' &&
      c.status === 'False' &&
      c.reason === 'RolloutAborted',
  )?.message;

const RolloutSummary = ({
  rollout,
  numberOfCurrentPods,
  numberOfPodsWithErrors,
  hpa,
}) => {
  const pauseTime = rollout.status?.pauseConditions?.find(
    (p) => p.reason === 'CanaryPauseStep',
  )?.startTime;
  const abortedMessage = findAbortedMessage(rollout);

  return (
    React.createElement(Grid, {
      container: true,
      direction: "row",
      justifyContent: "flex-start",
      alignItems: "center",}
    
      , React.createElement(Grid, { xs: 3, item: true,}
        , React.createElement(RolloutDrawer, { rollout: rollout,} )
      )
      , React.createElement(Grid, { item: true, xs: 1,}
        , React.createElement(Divider, { style: { height: '5em' }, orientation: "vertical",} )
      )
      , hpa && (
        React.createElement(Grid, { item: true, xs: 3,}
          , React.createElement(HorizontalPodAutoscalerDrawer, { hpa: hpa,}
            , React.createElement(Grid, {
              item: true,
              container: true,
              direction: "column",
              justifyContent: "flex-start",
              alignItems: "flex-start",
              spacing: 0,}
            
              , React.createElement(Grid, { item: true,}
                , React.createElement(Typography, { variant: "subtitle2",}, "min replicas "
                    , hpa.spec?.minReplicas ?? '?', " / max replicas"   , ' '
                  , hpa.spec?.maxReplicas ?? '?'
                )
              )
              , React.createElement(Grid, { item: true,}
                , React.createElement(Typography, { variant: "subtitle2",}, "current CPU usage:"
                    , ' '
                  , hpa.status?.currentCPUUtilizationPercentage ?? '?', "%"
                )
              )
              , React.createElement(Grid, { item: true,}
                , React.createElement(Typography, { variant: "subtitle2",}, "target CPU usage:"
                    , ' '
                  , hpa.spec?.targetCPUUtilizationPercentage ?? '?', "%"
                )
              )
            )
          )
        )
      )
      , React.createElement(Grid, {
        item: true,
        container: true,
        xs: 3,
        direction: "column",
        justifyContent: "flex-start",
        alignItems: "flex-start",}
      
        , React.createElement(Grid, { item: true,}
          , React.createElement(StatusOK, null, numberOfCurrentPods, " pods" )
        )
        , React.createElement(Grid, { item: true,}
          , numberOfPodsWithErrors > 0 ? (
            React.createElement(StatusError, null
              , numberOfPodsWithErrors, " pod"
              , numberOfPodsWithErrors > 1 ? 's' : '', " with errors"
            )
          ) : (
            React.createElement(StatusOK, null, "No pods with errors"   )
          )
        )
      )
      , pauseTime && (
        React.createElement(Grid, { item: true, xs: 3,}
          , React.createElement('div', {
            style: {
              display: 'flex',
              alignItems: 'center',
              flexWrap: 'wrap',
            },}
          
            , React.createElement(PauseIcon, null )
            , React.createElement(Typography, { variant: "subtitle1",}, "Paused ("
               , DateTime.fromISO(pauseTime).toRelative({ locale: 'en' }), ")"

            )
          )
        )
      )
      , abortedMessage && (
        React.createElement(Grid, { item: true, xs: 3,}
          , AbortedTitle
        )
      )
    )
  );
};

const RolloutAccordion = ({
  rollout,
  ownedPods,
  matchingHpa,
  defaultExpanded,
}) => {
  const podNamesWithErrors = useContext(PodNamesWithErrorsContext);

  const podsWithErrors = ownedPods.filter(p =>
    podNamesWithErrors.has(p.metadata?.name ?? ''),
  );

  const currentStepIndex = rollout.status?.currentStepIndex ?? 0;
  const abortedMessage = findAbortedMessage(rollout);

  return (
    React.createElement(Accordion, {
      defaultExpanded: defaultExpanded,
      TransitionProps: { unmountOnExit: true },}
    
      , React.createElement(AccordionSummary, { expandIcon: React.createElement(ExpandMoreIcon, null ),}
        , React.createElement(RolloutSummary, {
          rollout: rollout,
          numberOfCurrentPods: ownedPods.length,
          numberOfPodsWithErrors: podsWithErrors.length,
          hpa: matchingHpa,}
        )
      )
      , React.createElement(AccordionDetails, null
        , React.createElement('div', { style: { width: '100%' },}
          , React.createElement('div', null
            , React.createElement(Typography, { variant: "h6",}, "Rollout status" )
          )
          , React.createElement('div', { style: { margin: '1rem' },}
            , abortedMessage && (
              React.createElement(React.Fragment, null
                , AbortedTitle
                , React.createElement(Typography, { variant: "subtitle2",}, abortedMessage)
              )
            )
            , React.createElement(StepsProgress, {
              aborted: abortedMessage !== undefined,
              steps: rollout.spec?.strategy?.canary?.steps ?? [],
              currentStepIndex: currentStepIndex,}
            )
          )
          , React.createElement('div', null
            , React.createElement(PodsTable, {
              pods: ownedPods,
              extraColumns: [READY_COLUMNS, RESOURCE_COLUMNS],}
            )
          )
        )
      )
    )
  );
};

export const RolloutAccordions = ({
  rollouts,
  defaultExpanded = false,
}) => {
  const groupedResponses = useContext(GroupedResponsesContext);

  return (
    React.createElement(Grid, {
      container: true,
      direction: "column",
      justifyContent: "flex-start",
      alignItems: "flex-start",}
    
      , rollouts.map((rollout, i) => (
        React.createElement(Grid, { container: true, item: true, key: i, xs: true,}
          , React.createElement(Grid, { item: true, xs: true,}
            , React.createElement(RolloutAccordion, {
              defaultExpanded: defaultExpanded,
              matchingHpa: getMatchingHpa(
                {
                  name: rollout.metadata?.name,
                  namespace: rollout.metadata?.namespace,
                  kind: 'rollout',
                },
                groupedResponses.horizontalPodAutoscalers,
              ),
              ownedPods: getOwnedPodsThroughReplicaSets(
                rollout,
                groupedResponses.replicaSets,
                groupedResponses.pods,
              ),
              rollout: rollout,}
            )
          )
        )
      ))
    )
  );
};
