    React.createElement(Routes, null
      , React.createElement(Route, { path: "/", element: React.createElement(CITable, null ),} )
      , React.createElement(Route, { path: `/${buildRouteRef.path}`, element: React.createElement(DetailedViewPage, null ),} )
    )
