import React, { cloneElement } from 'react';
import useObservable from 'react-use/lib/useObservable';
import AutoIcon from '@material-ui/icons/BrightnessAuto';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import {
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Tooltip,
  makeStyles,
} from '@material-ui/core';
import { appThemeApiRef, useApi } from '@backstage/core-plugin-api';







const ThemeIcon = ({ id, activeId, icon }) =>
  icon ? (
    cloneElement(icon, {
      color: activeId === id ? 'primary' : undefined,
    })
  ) : (
    React.createElement(AutoIcon, { color: activeId === id ? 'primary' : undefined,} )
  );







const useStyles = makeStyles(theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingBottom: 8,
    paddingRight: 16,
  },
  list: {
    width: 'initial',
    [theme.breakpoints.down('xs')]: {
      width: '100%',
      padding: `0 0 12px`,
    },
  },
  listItemText: {
    paddingRight: 0,
    paddingLeft: 0,
  },
  listItemSecondaryAction: {
    position: 'relative',
    transform: 'unset',
    top: 'auto',
    right: 'auto',
    paddingLeft: 16,
    [theme.breakpoints.down('xs')]: {
      paddingLeft: 0,
    },
  },
}));

// ToggleButtonGroup uses React.children.map instead of context
// so wrapping with Tooltip breaks ToggleButton functionality.
const TooltipToggleButton = ({
  children,
  title,
  value,
  ...props
}) => (
  React.createElement(Tooltip, { placement: "top", arrow: true, title: title,}
    , React.createElement(ToggleButton, { value: value, ...props,}
      , children
    )
  )
);

export const UserSettingsThemeToggle = () => {
  const classes = useStyles();
  const appThemeApi = useApi(appThemeApiRef);
  const themeId = useObservable(
    appThemeApi.activeThemeId$(),
    appThemeApi.getActiveThemeId(),
  );

  const themeIds = appThemeApi.getInstalledThemes();

  const handleSetTheme = (
    _event,
    newThemeId,
  ) => {
    if (themeIds.some(t => t.id === newThemeId)) {
      appThemeApi.setActiveThemeId(newThemeId);
    } else {
      appThemeApi.setActiveThemeId(undefined);
    }
  };

  return (
    React.createElement(ListItem, {
      className: classes.list,
      classes: { container: classes.container },}
    
      , React.createElement(ListItemText, {
        className: classes.listItemText,
        primary: "Theme",
        secondary: "Change the theme mode"   ,}
      )
      , React.createElement(ListItemSecondaryAction, { className: classes.listItemSecondaryAction,}
        , React.createElement(ToggleButtonGroup, {
          exclusive: true,
          size: "small",
          value: themeId ?? 'auto',
          onChange: handleSetTheme,}
        
          , themeIds.map(theme => {
            const themeIcon = themeIds.find(t => t.id === theme.id)?.icon;
            return (
              React.createElement(TooltipToggleButton, {
                key: theme.id,
                title: `Select ${theme.title}`,
                value: theme.id,}
              
                , React.createElement(React.Fragment, null
                  , theme.title, " "
                  , React.createElement(ThemeIcon, {
                    id: theme.id,
                    icon: themeIcon,
                    activeId: themeId,}
                  )
                )
              )
            );
          })
          , React.createElement(Tooltip, { placement: "top", arrow: true, title: "Select auto theme"  ,}
            , React.createElement(ToggleButton, { value: "auto", selected: themeId === undefined,}, "Auto "

              , React.createElement(AutoIcon, { color: themeId === undefined ? 'primary' : undefined,} )
            )
          )
        )
      )
    )
  );
};
