import React from 'react';
import { Link, Typography, Box, IconButton, Tooltip } from '@material-ui/core';
import RetryIcon from '@material-ui/icons/Replay';
import GoogleIcon from '@material-ui/icons/CloudCircle';
import { Link as RouterLink } from 'react-router-dom';
import { useWorkflowRuns, } from '../useWorkflowRuns';
import { WorkflowRunStatus } from '../WorkflowRunStatus';
import SyncIcon from '@material-ui/icons/Sync';
import { useProjectName } from '../useProjectName';

import { buildRouteRef } from '../../routes';
import { DateTime } from 'luxon';
import { Table, } from '@backstage/core-components';
import { useRouteRef } from '@backstage/core-plugin-api';

const generatedColumns = [
  {
    title: 'Status',
    width: '150px',

    render: (row) => (
      React.createElement(Box, { display: "flex", alignItems: "center",}
        , React.createElement(WorkflowRunStatus, { status: row.status,} )
      )
    ),
  },
  {
    title: 'Build',
    field: 'id',
    type: 'numeric',
    width: '150px',
    render: (row) => (
      React.createElement(Typography, { variant: "body2", noWrap: true,}
        , row.id?.substring(0, 8)
      )
    ),
  },
  {
    title: 'Source',
    field: 'source',
    highlight: true,
    width: '200px',
    render: (row) => {
      const LinkWrapper = () => {
        const routeLink = useRouteRef(buildRouteRef);
        return (
          React.createElement(Link, {
            component: RouterLink,
            'data-testid': "cell-source",
            to: routeLink({ id: row.id }),}
          
            , row.message
          )
        );
      };

      return React.createElement(LinkWrapper, null );
    },
  },
  {
    title: 'Ref',
    render: (row) => (
      React.createElement(Typography, { variant: "body2", noWrap: true,}
        , row.substitutions?.BRANCH_NAME
      )
    ),
  },
  {
    title: 'Commit',
    render: (row) => (
      React.createElement(Typography, { variant: "body2", noWrap: true,}
        , row.substitutions?.SHORT_SHA
      )
    ),
  },
  {
    title: 'Created',
    render: (row) => (
      React.createElement(Typography, { 'data-testid': "cell-created", variant: "body2", noWrap: true,}
        , DateTime.fromISO(row.createTime ?? DateTime.now().toISO()).toFormat(
          'dd-MM-yyyy hh:mm:ss',
        )
      )
    ),
  },
  {
    title: 'Actions',
    render: (row) => (
      React.createElement(Tooltip, { title: "Rerun workflow" ,}
        , React.createElement(IconButton, { 'data-testid': "action-rerun", onClick: row.rerun,}
          , React.createElement(RetryIcon, null )
        )
      )
    ),
    width: '10%',
  },
];













export const WorkflowRunsTableView = ({
  projectName,
  loading,
  pageSize,
  page,
  retry,
  runs,
  onChangePage,
  onChangePageSize,
  total,
}) => {
  return (
    React.createElement(Table, {
      isLoading: loading,
      options: { paging: true, pageSize, padding: 'dense' },
      totalCount: total,
      page: page,
      actions: [
        {
          icon: () => React.createElement(SyncIcon, null ),
          tooltip: 'Reload workflow runs',
          isFreeAction: true,
          onClick: () => retry(),
        },
      ],
      data: runs ?? [],
      onPageChange: onChangePage,
      onRowsPerPageChange: onChangePageSize,
      style: { width: '100%' },
      title: 
        React.createElement(Box, { display: "flex", alignItems: "center",}
          , React.createElement(GoogleIcon, null )
          , React.createElement(Box, { mr: 1,} )
          , React.createElement(Typography, { variant: "h6",}, projectName)
        )
      ,
      columns: generatedColumns,}
    )
  );
};

export const WorkflowRunsTable = ({ entity }) => {
  const { value: projectName, loading } = useProjectName(entity);
  const [projectId] = (projectName ?? '/').split('/');

  const [tableProps, { retry, setPage, setPageSize }] = useWorkflowRuns({
    projectId,
  });

  return (
    React.createElement(WorkflowRunsTableView, {
      ...tableProps,
      loading: loading || tableProps.loading,
      retry: retry,
      onChangePageSize: setPageSize,
      onChangePage: setPage,}
    )
  );
};
