
















import makeStyles from '@material-ui/core/styles/makeStyles';
import React from 'react';

import classNames from 'classnames';

const useStyles = makeStyles((theme) => ({
  text: {
    fill: theme.palette.textContrast,
  },
  secondary: {
    fill: theme.palette.textSubtle,
  },
}));

export function CustomLabel({
  edge: { relations },
}) {
  const classes = useStyles();
  return (
    React.createElement('text', { className: classes.text, textAnchor: "middle",}
      , relations.map((r, i) => (
        React.createElement('tspan', { key: r, className: classNames(i > 0 && classes.secondary),}
          , i > 0 && React.createElement('tspan', null, " / "  )
          , r
        )
      ))
    )
  );
}
