import React, { useEffect, useState } from 'react';
import {
  Button,
  ListItem,
  ListItemIcon,
  ListItemSecondaryAction,
  ListItemText,
  Tooltip,
} from '@material-ui/core';
import {


  useApi,

  SessionState,
} from '@backstage/core-plugin-api';








export const ProviderSettingsItem = ({
  title,
  description,
  icon: Icon,
  apiRef,
}) => {
  const api = useApi(apiRef);
  const [signedIn, setSignedIn] = useState(false);

  useEffect(() => {
    let didCancel = false;

    const subscription = api
      .sessionState$()
      .subscribe((sessionState) => {
        if (!didCancel) {
          setSignedIn(sessionState === SessionState.SignedIn);
        }
      });

    return () => {
      didCancel = true;
      subscription.unsubscribe();
    };
  }, [api]);

  return (
    React.createElement(ListItem, null
      , React.createElement(ListItemIcon, null
        , React.createElement(Icon, null )
      )
      , React.createElement(ListItemText, {
        primary: title,
        secondary: 
          React.createElement(Tooltip, { placement: "top", arrow: true, title: description,}
            , React.createElement('span', null, description)
          )
        ,
        secondaryTypographyProps: { noWrap: true, style: { width: '80%' } },}
      )
      , React.createElement(ListItemSecondaryAction, null
        , React.createElement(Tooltip, {
          placement: "top",
          arrow: true,
          title: signedIn ? `Sign out from ${title}` : `Sign in to ${title}`,}
        
          , React.createElement(Button, {
            variant: "outlined",
            color: "primary",
            onClick: () => (signedIn ? api.signOut() : api.signIn()),}
          
            , signedIn ? `Sign out` : `Sign in`
          )
        )
      )
    )
  );
};
