
















import React from 'react';
import { useTopActiveItems } from '../../hooks/useTopActiveItems';
import { RollbarTopItemsTable } from '../RollbarTopItemsTable/RollbarTopItemsTable';





export const RollbarProject = ({ entity }) => {
  const { items, organization, project, loading, error } =
    useTopActiveItems(entity);

  return (
    React.createElement(RollbarTopItemsTable, {
      organization: organization,
      project: project,
      items: items || [],
      loading: loading,
      error: error,}
    )
  );
};
