import { createApiRef, } from '@backstage/core-plugin-api';


import { ResponseError } from '@backstage/errors';






export const gcalendarApiRef = createApiRef({
  id: 'plugin.gcalendar.service',
});

export class GCalendarApiClient {
    authApi;
    fetchApi;

  constructor(options) {
    this.authApi = options.authApi;
    this.fetchApi = options.fetchApi;
  }

   async get(
    path,
    params,
  ) {
    const query = new URLSearchParams(params);
    const url = new URL(
      `${path}?${query.toString()}`,
      'https://www.googleapis.com',
    );
    const token = await this.authApi.getAccessToken();
    const response = await this.fetchApi.fetch(url.toString(), {
      headers: token ? { Authorization: `Bearer ${token}` } : {},
    });

    if (!response.ok) {
      throw await ResponseError.fromResponse(response);
    }

    return response.json() ;
  }

   async getCalendars(params) {
    return this.get(
      '/calendar/v3/users/me/calendarList',
      params,
    );
  }

   async getEvents(calendarId, params) {
    return this.get(
      `/calendar/v3/calendars/${encodeURIComponent(calendarId)}/events`,
      params,
    );
  }
}
