import {

  getMe,
  getBuildSummaries,
  getFullBuild,
  postBuildActions,
  BuildAction,
  BuildWithSteps,
  BuildStepAction,
  BuildSummary,
  GitType,
} from 'circleci-api';
import { createApiRef, } from '@backstage/core-plugin-api';

export { GitType };
;

export const circleCIApiRef = createApiRef({
  id: 'plugin.circleci.service',
});

const DEFAULT_PROXY_PATH = '/circleci/api';









export class CircleCIApi {
    discoveryApi;
    proxyPath;

  constructor(options) {
    this.discoveryApi = options.discoveryApi;
    this.proxyPath = options.proxyPath ?? DEFAULT_PROXY_PATH;
  }

  async retry(buildNumber, options) {
    return postBuildActions('', buildNumber, BuildAction.RETRY, {
      circleHost: await this.getApiUrl(),
      ...options.vcs,
    });
  }

  async getBuilds(
    { limit = 10, offset = 0 },
    options,
  ) {
    return getBuildSummaries('', {
      options: {
        limit,
        offset,
      },
      vcs: {},
      circleHost: await this.getApiUrl(),
      ...options,
    });
  }

  async getUser(options) {
    return getMe('', { circleHost: await this.getApiUrl(), ...options });
  }

  async getBuild(buildNumber, options) {
    return getFullBuild('', buildNumber, {
      circleHost: await this.getApiUrl(),
      ...options.vcs,
    });
  }

   async getApiUrl() {
    const proxyUrl = await this.discoveryApi.getBaseUrl('proxy');
    return proxyUrl + this.proxyPath;
  }
}
