import {
  Box,
  Checkbox,
  FormControlLabel,
  FormHelperText,
  StepLabel,
  TextField,
  Typography,
} from '@material-ui/core';
import React from 'react';
import { BackButton } from '../Buttons';
import { asInputRef } from '../helpers';
import { StepFinishImportLocation } from '../StepFinishImportLocation';
import { StepInitAnalyzeUrl } from '../StepInitAnalyzeUrl';
import {
  AutocompleteTextField,
  StepPrepareCreatePullRequest,
} from '../StepPrepareCreatePullRequest';
import { StepPrepareSelectLocations } from '../StepPrepareSelectLocations';
import { StepReviewLocation } from '../StepReviewLocation';
































/**
 * The default stepper generation function.
 *
 * Override this function to customize the import flow. Each flow should at
 * least override the prepare operation.
 *
 * @param flow - the name of the active flow
 * @param defaults - the default steps
 * @public
 */
export function defaultGenerateStepper(
  flow,
  defaults,
) {
  switch (flow) {
    // the prepare step is skipped but the label of the step is updated
    case 'single-location':
      return {
        ...defaults,
        prepare: () => ({
          stepLabel: (
            React.createElement(StepLabel, {
              optional: 
                React.createElement(Typography, { variant: "caption",}, "Discovered Locations: 1"

                )
              ,}
            , "Select Locations"

            )
          ),
          content: React.createElement(React.Fragment, null),
        }),
      };

    // let the user select one or more of the discovered locations in the prepare step
    case 'multiple-locations':
      return {
        ...defaults,
        prepare: (state, opts) => {
          if (state.analyzeResult.type !== 'locations') {
            return defaults.prepare(state, opts);
          }

          return {
            stepLabel: (
              React.createElement(StepLabel, {
                optional: 
                  React.createElement(Typography, { variant: "caption",}, "Discovered Locations: "
                      , state.analyzeResult.locations.length
                  )
                ,}
              , "Select Locations"

              )
            ),
            content: (
              React.createElement(StepPrepareSelectLocations, {
                analyzeResult: state.analyzeResult,
                prepareResult: state.prepareResult,
                onPrepare: state.onPrepare,
                onGoBack: state.onGoBack,}
              )
            ),
          };
        },
      };

    case 'no-location':
      return {
        ...defaults,
        prepare: (state, opts) => {
          if (state.analyzeResult.type !== 'repository') {
            return defaults.prepare(state, opts);
          }

          return {
            stepLabel: React.createElement(StepLabel, null, "Create Pull Request"  ),
            content: (
              React.createElement(StepPrepareCreatePullRequest, {
                analyzeResult: state.analyzeResult,
                onPrepare: state.onPrepare,
                onGoBack: state.onGoBack,
                renderFormFields: ({
                  values,
                  setValue,
                  formState,
                  groupsLoading,
                  groups,
                  register,
                }) => (
                  React.createElement(React.Fragment, null
                    , React.createElement(Box, { marginTop: 2,}
                      , React.createElement(Typography, { variant: "h6",}, "Pull Request Details"  )
                    )

                    , React.createElement(TextField, {
                      ...asInputRef(
                        register('title', {
                          required: true,
                        }),
                      ),
                      label: "Pull Request Title"  ,
                      placeholder: "Add Backstage catalog entity descriptor files"     ,
                      margin: "normal",
                      variant: "outlined",
                      fullWidth: true,
                      error: Boolean(formState.errors.title),
                      required: true,}
                    )

                    , React.createElement(TextField, {
                      ...asInputRef(
                        register('body', {
                          required: true,
                        }),
                      ),
                      label: "Pull Request Body"  ,
                      placeholder: "A describing text with Markdown support"     ,
                      margin: "normal",
                      variant: "outlined",
                      fullWidth: true,
                      error: Boolean(formState.errors.body),
                      multiline: true,
                      required: true,}
                    )

                    , React.createElement(Box, { marginTop: 2,}
                      , React.createElement(Typography, { variant: "h6",}, "Entity Configuration" )
                    )

                    , React.createElement(TextField, {
                      ...asInputRef(
                        register('componentName', { required: true }),
                      ),
                      label: "Name of the created component"    ,
                      placeholder: "my-component",
                      margin: "normal",
                      variant: "outlined",
                      fullWidth: true,
                      error: Boolean(formState.errors.componentName),
                      required: true,}
                    )

                    , !values.useCodeowners && (
                      React.createElement(AutocompleteTextField, {
                        name: "owner",
                        errors: formState.errors,
                        options: groups || [],
                        loading: groupsLoading,
                        loadingText: "Loading groups…" ,
                        helperText: "Select an owner from the list or enter a reference to a Group or a User"               ,
                        errorHelperText: "required value" ,
                        textFieldProps: {
                          label: 'Entity Owner',
                          placeholder: 'my-group',
                        },
                        rules: { required: true },
                        required: true,}
                      )
                    )

                    , React.createElement(FormControlLabel, {
                      control: 
                        React.createElement(Checkbox, {
                          ...asInputRef(register('useCodeowners')),
                          onChange: (_, value) => {
                            if (value) {
                              setValue('owner', '');
                            }
                          },}
                        )
                      ,
                      label: 
                        React.createElement(React.Fragment, null, "Use "
                           , React.createElement('em', null, "CODEOWNERS"), " file as Entity Owner"
                        )
                      ,}
                    )
                    , React.createElement(FormHelperText, null, "WARNING: This may fail if no CODEOWNERS file is found at the target location."


                    )
                  )
                ),}
              )
            ),
          };
        },
      };

    default:
      return defaults;
  }
}

export const defaultStepper = {
  analyze: (state, { apis }) => ({
    stepLabel: React.createElement(StepLabel, null, "Select URL" ),
    content: (
      React.createElement(StepInitAnalyzeUrl, {
        key: "analyze",
        analysisUrl: state.analysisUrl,
        onAnalysis: state.onAnalysis,
        disablePullRequest: !apis.catalogImportApi.preparePullRequest,}
      )
    ),
  }),

  prepare: state => ({
    stepLabel: (
      React.createElement(StepLabel, { optional: React.createElement(Typography, { variant: "caption",}, "Optional"),}, "Import Actions"

      )
    ),
    content: React.createElement(BackButton, { onClick: state.onGoBack,} ),
  }),

  review: state => ({
    stepLabel: React.createElement(StepLabel, null, "Review"),
    content: (
      React.createElement(StepReviewLocation, {
        prepareResult: state.prepareResult,
        onReview: state.onReview,
        onGoBack: state.onGoBack,}
      )
    ),
  }),

  finish: state => ({
    stepLabel: React.createElement(StepLabel, null, "Finish"),
    content: (
      React.createElement(StepFinishImportLocation, {
        prepareResult: state.prepareResult,
        onReset: state.onReset,}
      )
    ),
  }),
};
