




















import React, { Fragment, } from 'react';
import { Chip } from '@material-ui/core';
import {
  StatusAborted,
  StatusError,
  StatusOK,
  SubvalueCell,
} from '@backstage/core-components';


export const imageChips = (pod) => {
  const containerStatuses = pod.status?.containerStatuses ?? [];
  const images = containerStatuses.map((cs, i) => {
    return React.createElement(Chip, { key: i, label: `${cs.name}=${cs.image}`, size: "small",} );
  });

  return React.createElement('div', null, images);
};

export const containersReady = (pod) => {
  const containerStatuses = pod.status?.containerStatuses ?? [];
  const containersReadyItem = containerStatuses.filter(cs => cs.ready).length;

  return `${containersReadyItem}/${containerStatuses.length}`;
};

export const totalRestarts = (pod) => {
  const containerStatuses = pod.status?.containerStatuses ?? [];
  return containerStatuses?.reduce((a, b) => a + b.restartCount, 0);
};

export const containerStatuses = (pod) => {
  const containerStatusesItem = pod.status?.containerStatuses ?? [];
  const errors = containerStatusesItem.reduce((accum, next) => {
    if (next.state === undefined) {
      return accum;
    }

    const waiting = next.state.waiting;
    const terminated = next.state.terminated;

    const renderCell = (reason) => (
      React.createElement(Fragment, { key: `${pod.metadata?.name}-${next.name}`,}
        , React.createElement(SubvalueCell, {
          value: 
            reason === 'Completed' ? (
              React.createElement(StatusOK, null, "Container: " , next.name)
            ) : (
              React.createElement(StatusError, null, "Container: " , next.name)
            )
          ,
          subvalue: reason,}
        )
        , React.createElement('br', null )
      )
    );

    if (waiting) {
      accum.push(renderCell(waiting.reason));
    }

    if (terminated) {
      accum.push(renderCell(terminated.reason));
    }

    return accum;
  }, [] );

  if (errors.length === 0) {
    return React.createElement(StatusOK, null, "OK");
  }

  return errors;
};

export const renderCondition = (
  condition,
) => {
  const status = condition.status;

  if (status === 'True') {
    return [condition.type, React.createElement(StatusOK, null, "True")];
  } else if (status === 'False') {
    return [
      condition.type,
      React.createElement(SubvalueCell, {
        value: React.createElement(StatusError, null, "False"),
        subvalue: condition.message ?? '',}
      ),
    ];
  }
  return [condition.type, React.createElement(StatusAborted, null )];
};

// visible for testing
export const currentToDeclaredResourceToPerc = (
  current,
  resource,
) => {
  if (Number(resource) === 0) return `0%`;

  if (typeof current === 'number' && typeof resource === 'number') {
    return `${Math.round((current / resource) * 100)}%`;
  }

  const numerator = BigInt(current);
  const denominator = BigInt(resource);

  return `${(numerator * BigInt(100)) / denominator}%`;
};

const formatMilicores = (value) => {
  return `${parseFloat(value.toString()) * 1000}m`;
};

export const podStatusToCpuUtil = (podStatus) => {
  const cpuUtil = podStatus.cpu;

  let currentUsage = cpuUtil.currentUsage;

  // current usage number for CPU is a different unit than request/limit total
  // this might be a bug in the k8s library
  if (typeof cpuUtil.currentUsage === 'number') {
    currentUsage = cpuUtil.currentUsage / 10;
  }

  return (
    React.createElement(SubvalueCell, {
      value: `requests: ${currentToDeclaredResourceToPerc(
        currentUsage,
        cpuUtil.requestTotal,
      )} of ${formatMilicores(cpuUtil.requestTotal)}`,
      subvalue: `limits: ${currentToDeclaredResourceToPerc(
        currentUsage,
        cpuUtil.limitTotal,
      )} of ${formatMilicores(cpuUtil.limitTotal)}`,}
    )
  );
};

const bytesToMiB = (value) => {
  return `${parseFloat(value.toString()) / 1024 / 1024}MiB`;
};

export const podStatusToMemoryUtil = (
  podStatus,
) => {
  const memUtil = podStatus.memory;

  return (
    React.createElement(SubvalueCell, {
      value: `requests: ${currentToDeclaredResourceToPerc(
        memUtil.currentUsage,
        memUtil.requestTotal,
      )} of ${bytesToMiB(memUtil.requestTotal)}`,
      subvalue: `limits: ${currentToDeclaredResourceToPerc(
        memUtil.currentUsage,
        memUtil.limitTotal,
      )} of ${bytesToMiB(memUtil.limitTotal)}`,}
    )
  );
};
