



















export class ProductionSentryApi  {
  constructor(
      discoveryApi,
      organization,
      identityApi,
  ) {;this.discoveryApi = discoveryApi;this.organization = organization;this.identityApi = identityApi;}

  async fetchIssues(
    project,
    statsFor,
    query,
  ) {
    if (!project) {
      return [];
    }

    const apiUrl = `${await this.discoveryApi.getBaseUrl('proxy')}/sentry/api`;
    const options = await this.authOptions();

    const queryPart = query ? `&query=${query}` : '';

    const response = await fetch(
      `${apiUrl}/0/projects/${this.organization}/${project}/issues/?statsPeriod=${statsFor}${queryPart}`,
      options,
    );

    if (response.status >= 400 && response.status < 600) {
      throw new Error('Failed fetching Sentry issues');
    }

    return (await response.json()) ;
  }

   async authOptions() {
    if (!this.identityApi) {
      return {};
    }
    const { token } = await this.identityApi.getCredentials();
    return {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
  }
}
