import { readGitHubIntegrationConfigs } from '@backstage/integration';
import { useEntity } from '@backstage/plugin-catalog-react';
import React, { useEffect } from 'react';
import { generatePath, Link as RouterLink } from 'react-router-dom';
import { GITHUB_ACTIONS_ANNOTATION } from '../getProjectNameFromEntity';
import { useWorkflowRuns } from '../useWorkflowRuns';
import { WorkflowRunStatus } from '../WorkflowRunStatus';
import { Typography } from '@material-ui/core';

import { configApiRef, errorApiRef, useApi } from '@backstage/core-plugin-api';
import {
  InfoCard,

  Link,
  Table,
} from '@backstage/core-components';

const firstLine = (message) => message.split('\n')[0];








export const RecentWorkflowRunsCard = ({
  branch,
  dense = false,
  limit = 5,
  variant,
}) => {
  const { entity } = useEntity();
  const config = useApi(configApiRef);
  const errorApi = useApi(errorApiRef);
  // TODO: Get github hostname from metadata annotation
  const hostname = readGitHubIntegrationConfigs(
    config.getOptionalConfigArray('integrations.github') ?? [],
  )[0].host;
  const [owner, repo] = (
    entity?.metadata.annotations?.[GITHUB_ACTIONS_ANNOTATION] ?? '/'
  ).split('/');
  const [{ runs = [], loading, error }] = useWorkflowRuns({
    hostname,
    owner,
    repo,
    branch,
    initialPageSize: limit,
  });
  useEffect(() => {
    if (error) {
      errorApi.post(error);
    }
  }, [error, errorApi]);

  const githubHost = hostname || 'github.com';

  return (
    React.createElement(InfoCard, {
      title: "Recent Workflow Runs"  ,
      subheader: branch ? `Branch: ${branch}` : 'All Branches',
      noPadding: true,
      variant: variant,}
    
      , !runs.length ? (
        React.createElement('div', { style: { textAlign: 'center' },}
          , React.createElement(Typography, { variant: "body1",}, "This component has GitHub Actions enabled, but no workflows were found."


          )
          , React.createElement(Typography, { variant: "body2",}
            , React.createElement(Link, { to: `https://${githubHost}/${owner}/${repo}/actions/new`,}, "Create a new workflow"

            )
          )
        )
      ) : (
        React.createElement(Table, {
          isLoading: loading,
          options: {
            search: false,
            paging: false,
            padding: dense ? 'dense' : 'default',
            toolbar: false,
          },
          columns: [
            {
              title: 'Commit Message',
              field: 'message',
              render: data => (
                React.createElement(Link, {
                  component: RouterLink,
                  to: generatePath('./ci-cd/:id', { id: data.id }),}
                
                  , firstLine(data.message)
                )
              ),
            },
            { title: 'Branch', field: 'source.branchName' },
            { title: 'Status', field: 'status', render: WorkflowRunStatus },
          ],
          data: runs,}
        )
      )
    )
  );
};
