import {
  Checkbox,
  Grid,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
} from '@material-ui/core';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import React, { useCallback, useState } from 'react';

import { BackButton, NextButton } from '../Buttons';
import { EntityListComponent } from '../EntityListComponent';

import partition from 'lodash/partition';








/**
 * A form that lets a user select one of a list of locations to import
 *
 * @param analyzeResult - the result of the analysis
 * @param prepareResult - the selectected locations from a previous step
 * @param onPrepare - called after the selection
 * @param onGoBack - called to go back to the previous step
 */
export const StepPrepareSelectLocations = ({
  analyzeResult,
  prepareResult,
  onPrepare,
  onGoBack,
}) => {
  const [selectedUrls, setSelectedUrls] = useState(
    prepareResult?.locations.map(l => l.target) || [],
  );

  const [existingLocations, locations] = partition(
    analyzeResult?.locations,
    l => l.exists,
  );

  const handleResult = useCallback(async () => {
    onPrepare({
      type: 'locations',
      locations: locations.filter((l) => selectedUrls.includes(l.target)),
    });
  }, [locations, onPrepare, selectedUrls]);

  const onItemClick = (url) => {
    setSelectedUrls(urls =>
      urls.includes(url) ? urls.filter(u => u !== url) : urls.concat(url),
    );
  };

  const onSelectAll = () => {
    setSelectedUrls(urls =>
      urls.length < locations.length ? locations.map(l => l.target) : [],
    );
  };

  return (
    React.createElement(React.Fragment, null
      , locations.length > 0 && (
        React.createElement(React.Fragment, null
          , React.createElement(Typography, null, "Select one or more locations that are present in your git repository:"


          )
          , React.createElement(EntityListComponent, {
            firstListItem: 
              React.createElement(ListItem, { dense: true, button: true, onClick: onSelectAll,}
                , React.createElement(ListItemIcon, null
                  , React.createElement(Checkbox, {
                    edge: "start",
                    checked: selectedUrls.length === locations.length,
                    indeterminate: 
                      selectedUrls.length > 0 &&
                      selectedUrls.length < locations.length
                    ,
                    tabIndex: -1,
                    disableRipple: true,}
                  )
                )
                , React.createElement(ListItemText, { primary: "Select All" ,} )
              )
            ,
            onItemClick: onItemClick,
            locations: locations,
            locationListItemIcon: target => (
              React.createElement(Checkbox, {
                edge: "start",
                checked: selectedUrls.includes(target),
                tabIndex: -1,
                disableRipple: true,}
              )
            ),
            collapsed: true,}
          )
        )
      )

      , existingLocations.length > 0 && (
        React.createElement(React.Fragment, null
          , React.createElement(Typography, null, "These locations already exist in the catalog:"      )
          , React.createElement(EntityListComponent, {
            locations: existingLocations,
            locationListItemIcon: () => React.createElement(LocationOnIcon, null ),
            withLinks: true,
            collapsed: true,}
          )
        )
      )

      , React.createElement(Grid, { container: true, spacing: 0,}
        , onGoBack && React.createElement(BackButton, { onClick: onGoBack,} )
        , React.createElement(NextButton, { disabled: selectedUrls.length === 0, onClick: handleResult,}, "Review"

        )
      )
    )
  );
};
