import { Box, Grid, Typography } from '@material-ui/core';
import RetryIcon from '@material-ui/icons/Replay';
import React from 'react';
import { useConsumerGroupsOffsetsForEntity } from './useConsumerGroupsOffsetsForEntity';
import { Table, } from '@backstage/core-components';








const generatedColumns = [
  {
    title: 'Topic',
    field: 'topic',
    highlight: true,
    render: (row) => {
      return React.createElement(React.Fragment, null, row.topic ?? '');
    },
  },
  {
    title: 'Partition',
    field: 'partitionId',
    render: (row) => {
      return React.createElement(React.Fragment, null, row.partitionId ?? '');
    },
  },
  {
    title: 'Topic Offset',
    field: 'topicOffset',
    render: (row) => {
      return React.createElement(React.Fragment, null, row.topicOffset ?? '');
    },
  },
  {
    title: 'Group Offset',
    field: 'groupOffset',
    render: (row) => {
      return React.createElement(React.Fragment, null, row.groupOffset ?? '');
    },
  },
  {
    title: 'Lag',
    field: 'lag',
    render: (row) => {
      let lag = undefined;
      if (row.topicOffset && row.groupOffset) {
        lag = +row.topicOffset - +row.groupOffset;
      }
      return React.createElement(React.Fragment, null, lag ?? '');
    },
  },
];









export const ConsumerGroupOffsets = ({
  loading,
  topics,
  clusterId,
  consumerGroup,
  retry,
}) => {
  return (
    React.createElement(Table, {
      isLoading: loading,
      actions: [
        {
          icon: () => React.createElement(RetryIcon, null ),
          tooltip: 'Refresh Data',
          isFreeAction: true,
          onClick: () => retry(),
        },
      ],
      data: topics ?? [],
      title: 
        React.createElement(Box, { display: "flex", alignItems: "center",}
          , React.createElement(Typography, { variant: "h6",}, "Consumed Topics for "
               , consumerGroup, " (" , clusterId, ")"
          )
        )
      ,
      columns: generatedColumns,}
    )
  );
};

export const KafkaTopicsForConsumer = () => {
  const [tableProps, { retry }] = useConsumerGroupsOffsetsForEntity();
  return (
    React.createElement(Grid, null
      , tableProps.consumerGroupsTopics?.map(consumerGroup => (
        React.createElement(ConsumerGroupOffsets, {
          ...consumerGroup,
          loading: tableProps.loading,
          retry: retry,}
        )
      ))
    )
  );
};
