


















import { equalsIgnoreCase } from '../../../../utils';













export function createCreatedByUserFilter(
  filter,
) {
  const email = filter.email;

  return (pullRequest) => {
    const uniqueName = pullRequest.createdBy?.uniqueName;

    if (!email || !uniqueName) {
      return false;
    }

    return equalsIgnoreCase(email, uniqueName);
  };
}
