
















import { LogViewer } from '@backstage/core-components';
import { configApiRef, useApi } from '@backstage/core-plugin-api';
import { readGitHubIntegrationConfigs } from '@backstage/integration';
import {
  Accordion,
  AccordionSummary,
  CircularProgress,
  Fade,
  makeStyles,
  Modal,

  Tooltip,
  Typography,
  Zoom,
} from '@material-ui/core';
import DescriptionIcon from '@material-ui/icons/Description';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import React from 'react';
import { getProjectNameFromEntity } from '../getProjectNameFromEntity';
import { useDownloadWorkflowRunLogs } from './useDownloadWorkflowRunLogs';

const useStyles = makeStyles(theme => ({
  button: {
    order: -1,
    marginRight: 0,
    marginLeft: '-20px',
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    width: '85%',
    height: '85%',
    justifyContent: 'center',
    margin: 'auto',
  },
  normalLogContainer: {
    height: '75vh',
    width: '100%',
  },
  modalLogContainer: {
    height: '100%',
    width: '100%',
  },
  log: {
    background: theme.palette.background.default,
  },
}));

/**
 * A component for Run Logs visualization.
 */
export const WorkflowRunLogs = ({
  entity,
  runId,
  inProgress,
}



) => {
  const config = useApi(configApiRef);
  const classes = useStyles();
  const projectName = getProjectNameFromEntity(entity);

  // TODO: Get github hostname from metadata annotation
  const hostname = readGitHubIntegrationConfigs(
    config.getOptionalConfigArray('integrations.github') ?? [],
  )[0].host;
  const [owner, repo] = (projectName && projectName.split('/')) || [];
  const jobLogs = useDownloadWorkflowRunLogs({
    hostname,
    owner,
    repo,
    id: runId,
  });
  const logText = jobLogs.value ? String(jobLogs.value) : undefined;
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    React.createElement(Accordion, { TransitionProps: { unmountOnExit: true }, disabled: inProgress,}
      , React.createElement(AccordionSummary, {
        expandIcon: React.createElement(ExpandMoreIcon, null ),
        'aria-controls': `panel-${name}-content`,
        id: `panel-${name}-header`,
        IconButtonProps: {
          className: classes.button,
        },}
      
        , React.createElement(Typography, { variant: "button",}
          , jobLogs.loading ? React.createElement(CircularProgress, null ) : 'Job Log'
        )
        , React.createElement(Tooltip, { title: "Open Log" , TransitionComponent: Zoom, arrow: true,}
          , React.createElement(DescriptionIcon, {
            onClick: event => {
              event.stopPropagation();
              handleOpen();
            },
            style: { marginLeft: 'auto' },}
          )
        )
        , React.createElement(Modal, {
          className: classes.modal,
          onClick: event => event.stopPropagation(),
          open: open,
          onClose: handleClose,}
        
          , React.createElement(Fade, { in: open,}
            , React.createElement('div', { className: classes.modalLogContainer,}
              , React.createElement(LogViewer, {
                text: logText ?? 'No Values Found',
                classes: { root: classes.log },}
              )
            )
          )
        )
      )
      , logText && (
        React.createElement('div', { className: classes.normalLogContainer,}
          , React.createElement(LogViewer, { text: logText, classes: { root: classes.log },} )
        )
      )
    )
  );
};
