import React from 'react';
import {
  ListItem,
  ListItemText,
  ListItemIcon,
  Switch,
  Tooltip,
} from '@material-ui/core';








export const FlagItem = ({ flag, enabled, toggleHandler }) => (
  React.createElement(ListItem, { divider: true, button: true, onClick: () => toggleHandler(flag.name),}
    , React.createElement(ListItemIcon, null
      , React.createElement(Tooltip, { placement: "top", arrow: true, title: enabled ? 'Disable' : 'Enable',}
        , React.createElement(Switch, { color: "primary", checked: enabled, name: flag.name,} )
      )
    )
    , React.createElement(ListItemText, {
      primary: flag.name,
      secondary: `Registered in ${flag.pluginId} plugin`,}
    )
  )
);
