import React, { useContext } from 'react';

import { PodDrawer } from './PodDrawer';
import {
  containersReady,
  containerStatuses,
  podStatusToCpuUtil,
  podStatusToMemoryUtil,
  totalRestarts,
} from '../../utils/pod';
import { Table, } from '@backstage/core-components';
import { PodNamesWithMetricsContext } from '../../hooks/PodNamesWithMetrics';

export const READY_COLUMNS = 'READY';
export const RESOURCE_COLUMNS = 'RESOURCE';








const DEFAULT_COLUMNS = [
  {
    title: 'name',
    highlight: true,
    render: (pod) => React.createElement(PodDrawer, { pod: pod,} ),
  },
  {
    title: 'phase',
    render: (pod) => pod.status?.phase ?? 'unknown',
  },
  {
    title: 'status',
    render: containerStatuses,
  },
];

const READY = [
  {
    title: 'containers ready',
    align: 'center',
    render: containersReady,
  },
  {
    title: 'total restarts',
    align: 'center',
    render: totalRestarts,
    type: 'numeric',
  },
];

export const PodsTable = ({ pods, extraColumns = [] }) => {
  const podNamesWithMetrics = useContext(PodNamesWithMetricsContext);
  const columns = [...DEFAULT_COLUMNS];

  if (extraColumns.includes(READY_COLUMNS)) {
    columns.push(...READY);
  }
  if (extraColumns.includes(RESOURCE_COLUMNS)) {
    const resourceColumns = [
      {
        title: 'CPU usage %',
        render: (pod) => {
          const metrics = podNamesWithMetrics.get(pod.metadata?.name ?? '');

          if (!metrics) {
            return 'unknown';
          }

          return podStatusToCpuUtil(metrics);
        },
      },
      {
        title: 'Memory usage %',
        render: (pod) => {
          const metrics = podNamesWithMetrics.get(pod.metadata?.name ?? '');

          if (!metrics) {
            return 'unknown';
          }

          return podStatusToMemoryUtil(metrics);
        },
      },
    ];
    columns.push(...resourceColumns);
  }

  const tableStyle = {
    minWidth: '0',
    width: '100%',
  };

  return (
    React.createElement('div', { style: tableStyle,}
      , React.createElement(Table, {
        options: { paging: true, search: false },
        data: pods,
        columns: columns,}
      )
    )
  );
};
