export class StorageBucket  {
   static noPropAccessProxyHandler = {
    get(target, prop) {
      if (prop in target) {
        return target[prop ];
      }
      throw new Error(
        'Direct property access is not allowed for StorageBuckets',
      );
    },
    set() {
      throw new Error(
        'Direct property access is not allowed for StorageBuckets',
      );
    },
  };

  static forStorage(storage, bucket) {
    const storageBucket = new StorageBucket(storage, bucket);
    return new Proxy(storageBucket, StorageBucket.noPropAccessProxyHandler);
  }

  static forLocalStorage(bucket) {
    return StorageBucket.forStorage(localStorage, bucket);
  }

   constructor(
      storage,
      bucket,
  ) {;this.storage = storage;this.bucket = bucket;}

  

  get length() {
    throw new Error('Method not implemented.');
  }

  clear() {
    this.storage.removeItem(this.bucket);
  }

  getItem(key) {
    return this.read()?.[key] ?? null;
  }

  key() {
    throw new Error('Method not implemented.');
  }

  removeItem(key) {
    const data = this.read();
    if (!data) {
      return;
    }

    if (key in data) {
      delete data[key];
      this.write(data);
    }
  }

  setItem(key, value) {
    const data = this.read() ?? {};
    data[key] = value;
    this.write(data);
  }

   read() {
    const bucketValue = this.storage.getItem(this.bucket);
    if (!bucketValue) {
      return undefined;
    }

    try {
      return JSON.parse(bucketValue);
    } catch {
      return undefined;
    }
  }

   write(data) {
    this.storage.setItem(this.bucket, JSON.stringify(data));
  }
}
