
















import { useApp } from '@backstage/core-plugin-api';
import {
  EntityRefLink,
  humanizeEntityRef,
} from '@backstage/plugin-catalog-react';
import {
  Collapse,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemSecondaryAction,
  ListItemText,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import WorkIcon from '@material-ui/icons/Work';
import React, { useState } from 'react';

const useStyles = makeStyles(theme => ({
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

function sortEntities(entities) {
  return entities.sort((a, b) =>
    humanizeEntityRef(a).localeCompare(humanizeEntityRef(b)),
  );
}

/**
 * Props for {@link EntityListComponent}.
 *
 * @public
 */












/**
 * Shows a result list of entities.
 *
 * @public
 */
export const EntityListComponent = (props) => {
  const {
    locations,
    collapsed = false,
    locationListItemIcon,
    onItemClick,
    firstListItem,
    withLinks = false,
  } = props;

  const app = useApp();
  const classes = useStyles();

  const [expandedUrls, setExpandedUrls] = useState([]);

  const handleClick = (url) => {
    setExpandedUrls(urls =>
      urls.includes(url) ? urls.filter(u => u !== url) : urls.concat(url),
    );
  };

  return (
    React.createElement(List, null
      , firstListItem
      , locations.map(r => (
        React.createElement(React.Fragment, { key: r.target,}
          , React.createElement(ListItem, {
            dense: true,
            button: Boolean(onItemClick) ,
            onClick: () => onItemClick?.call(this, r.target),}
          
            , React.createElement(ListItemIcon, null, locationListItemIcon(r.target))

            , React.createElement(ListItemText, {
              primary: r.target,
              secondary: `Entities: ${r.entities.length}`,}
            )

            , collapsed && (
              React.createElement(ListItemSecondaryAction, null
                , React.createElement(IconButton, { edge: "end", onClick: () => handleClick(r.target),}
                  , expandedUrls.includes(r.target) ? (
                    React.createElement(ExpandLessIcon, null )
                  ) : (
                    React.createElement(ExpandMoreIcon, null )
                  )
                )
              )
            )
          )

          , React.createElement(Collapse, {
            in: !collapsed || expandedUrls.includes(r.target),
            timeout: "auto",
            unmountOnExit: true,}
          
            , React.createElement(List, { component: "div", disablePadding: true, dense: true,}
              , sortEntities(r.entities).map(entity => {
                const Icon =
                  app.getSystemIcon(
                    `kind:${entity.kind.toLocaleLowerCase('en-US')}`,
                  ) ?? WorkIcon;
                return (
                  React.createElement(ListItem, {
                    key: humanizeEntityRef(entity),
                    className: classes.nested,
                    ...(withLinks
                      ? {
                          component: EntityRefLink,
                          entityRef: entity,
                          button: withLinks ,
                        }
                      : {}),}
                  
                    , React.createElement(ListItemIcon, null
                      , React.createElement(Icon, null )
                    )
                    , React.createElement(ListItemText, { primary: humanizeEntityRef(entity),} )
                  )
                );
              })
            )
          )
        )
      ))
    )
  );
};
