import React, { useContext } from 'react';
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  Grid,
  Typography,
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

import { ServiceDrawer } from './ServiceDrawer';
import { GroupedResponsesContext } from '../../hooks';
import { StructuredMetadataTable } from '@backstage/core-components';





const ServiceSummary = ({ service }) => {
  return (
    React.createElement(Grid, {
      container: true,
      direction: "row",
      justifyContent: "flex-start",
      alignItems: "center",}
    
      , React.createElement(Grid, { xs: 3, item: true,}
        , React.createElement(ServiceDrawer, { service: service,} )
      )

      , React.createElement(Grid, { item: true, xs: 1,}
        , React.createElement(Divider, { style: { height: '5em' }, orientation: "vertical",} )
      )

      , React.createElement(Grid, { item: true,}
        , React.createElement(Typography, { variant: "subtitle2",}, "Type: "
           , service.spec?.type ?? '?'
        )
      )
    )
  );
};





const ServiceCard = ({ service }) => {
  const metadata = {};

  if (service.status?.loadBalancer?.ingress?.length ?? -1 > 0) {
    metadata.loadbalancer = service.status?.loadBalancer;
  }

  if (service.spec?.type === 'ClusterIP') {
    metadata.clusterIP = service.spec.clusterIP;
  }
  if (service.spec?.type === 'ExternalName') {
    metadata.externalName = service.spec.externalName;
  }

  return (
    React.createElement(StructuredMetadataTable, {
      metadata: {
        type: service.spec?.type,
        ports: service.spec?.ports,
        ...metadata,
      },}
    )
  );
};







const ServiceAccordion = ({ service }) => {
  return (
    React.createElement(Accordion, { TransitionProps: { unmountOnExit: true },}
      , React.createElement(AccordionSummary, { expandIcon: React.createElement(ExpandMoreIcon, null ),}
        , React.createElement(ServiceSummary, { service: service,} )
      )
      , React.createElement(AccordionDetails, null
        , React.createElement(ServiceCard, { service: service,} )
      )
    )
  );
};

export const ServicesAccordions = ({}) => {
  const groupedResponses = useContext(GroupedResponsesContext);
  return (
    React.createElement(Grid, {
      container: true,
      direction: "row",
      justifyContent: "flex-start",
      alignItems: "flex-start",}
    
      , groupedResponses.services.map((service, i) => (
        React.createElement(Grid, { item: true, key: i, xs: true,}
          , React.createElement(ServiceAccordion, { service: service,} )
        )
      ))
    )
  );
};
