import React from 'react';
import {
  DialogActions,
  DialogContent,
  DialogTitle,
  Grid,
  List,
  makeStyles,
  Paper,
  useTheme,
} from '@material-ui/core';
import LaunchIcon from '@material-ui/icons/Launch';
import { Link, useContent } from '@backstage/core-components';
import { useApi, useRouteRef } from '@backstage/core-plugin-api';
import { CatalogSearchResultListItem } from '@backstage/plugin-catalog';
import {
  catalogApiRef,
  CATALOG_FILTER_EXISTS,
} from '@backstage/plugin-catalog-react';
import {
  DefaultResultListItem,
  SearchBar,
  SearchFilter,
  searchPlugin,
  SearchResult,
  SearchResultPager,
  SearchType,
} from '@backstage/plugin-search';
import { useSearch } from '@backstage/plugin-search-react';
import { TechDocsSearchResultListItem } from '@backstage/plugin-techdocs';

const useStyles = makeStyles(theme => ({
  container: {
    borderRadius: 30,
    display: 'flex',
    height: '2.4em',
  },
  filter: {
    '& + &': {
      marginTop: theme.spacing(2.5),
    },
  },
  filters: {
    padding: theme.spacing(2),
    marginTop: theme.spacing(2),
  },
  input: {
    flex: 1,
  },
  dialogActionsContainer: { padding: theme.spacing(1, 3) },
  viewResultsLink: { verticalAlign: '0.5em' },
}));

export const SearchModal = ({ toggleModal }) => {
  const getSearchLink = useRouteRef(searchPlugin.routes.root);
  const classes = useStyles();

  const catalogApi = useApi(catalogApiRef);
  const { term, types } = useSearch();
  const { focusContent } = useContent();
  const { transitions } = useTheme();

  const handleResultClick = () => {
    toggleModal();
    setTimeout(focusContent, transitions.duration.leavingScreen);
  };

  const handleKeyPress = () => {
    handleResultClick();
  };

  return (
    React.createElement(React.Fragment, null
      , React.createElement(DialogTitle, null
        , React.createElement(Paper, { className: classes.container,}
          , React.createElement(SearchBar, { className: classes.input,} )
        )
      )
      , React.createElement(DialogContent, null
        , React.createElement(Grid, { container: true, direction: "column",}
          , React.createElement(Grid, { item: true,}
            , React.createElement(SearchType.Tabs, {
              defaultValue: "software-catalog",
              types: [
                {
                  value: 'software-catalog',
                  name: 'Software Catalog',
                },
                {
                  value: 'techdocs',
                  name: 'Documentation',
                },
              ],}
            )
          )
          , React.createElement(Grid, { item: true, container: true,}
            , types.includes('techdocs') && (
              React.createElement(Grid, { item: true, xs: 2,}
                , React.createElement(SearchFilter.Select, {
                  className: classes.filter,
                  label: "Entity",
                  name: "name",
                  values: async () => {
                    // Return a list of entities which are documented.
                    const { items } = await catalogApi.getEntities({
                      fields: ['metadata.name'],
                      filter: {
                        'metadata.annotations.backstage.io/techdocs-ref':
                          CATALOG_FILTER_EXISTS,
                      },
                    });

                    const names = items.map(entity => entity.metadata.name);
                    names.sort();
                    return names;
                  },}
                )
              )
            )
            , React.createElement(Grid, { item: true, xs: 2,}
              , React.createElement(SearchFilter.Select, {
                className: classes.filter,
                label: "Kind",
                name: "kind",
                values: ['Component', 'Template'],}
              )
            )
            , React.createElement(Grid, { item: true, xs: 2,}
              , React.createElement(SearchFilter.Select, {
                className: classes.filter,
                label: "Lifecycle",
                name: "lifecycle",
                values: ['experimental', 'production'],}
              )
            )
            , React.createElement(Grid, {
              item: true,
              xs: types.includes('techdocs') ? 6 : 8,
              container: true,
              direction: "row-reverse",
              justifyContent: "flex-start",
              alignItems: "center",}
            
              , React.createElement(Grid, { item: true,}
                , React.createElement(Link, {
                  onClick: () => {
                    toggleModal();
                    setTimeout(
                      focusContent,
                      transitions.duration.leavingScreen,
                    );
                  },
                  to: `${getSearchLink()}?query=${term}`,}
                
                  , React.createElement('span', { className: classes.viewResultsLink,}, "View Full Results"

                  )
                  , React.createElement(LaunchIcon, { color: "primary",} )
                )
              )
            )
          )
          , React.createElement(Grid, { item: true, xs: true,}
            , React.createElement(SearchResult, null
              , ({ results }) => (
                React.createElement(List, null
                  , results.map(({ type, document, highlight }) => {
                    let resultItem;
                    switch (type) {
                      case 'software-catalog':
                        resultItem = (
                          React.createElement(CatalogSearchResultListItem, {
                            key: document.location,
                            result: document,
                            highlight: highlight,}
                          )
                        );
                        break;
                      case 'techdocs':
                        resultItem = (
                          React.createElement(TechDocsSearchResultListItem, {
                            key: document.location,
                            result: document,
                            highlight: highlight,}
                          )
                        );
                        break;
                      default:
                        resultItem = (
                          React.createElement(DefaultResultListItem, {
                            key: document.location,
                            result: document,
                            highlight: highlight,}
                          )
                        );
                    }
                    return (
                      React.createElement('div', {
                        role: "button",
                        tabIndex: 0,
                        key: `${document.location}-btn`,
                        onClick: handleResultClick,
                        onKeyPress: handleKeyPress,}
                      
                        , resultItem
                      )
                    );
                  })
                )
              )
            )
          )
        )
      )
      , React.createElement(DialogActions, { className: classes.dialogActionsContainer,}
        , React.createElement(Grid, { container: true, direction: "row",}
          , React.createElement(Grid, { item: true, xs: 12,}
            , React.createElement(SearchResultPager, null )
          )
        )
      )
    )
  );
};
