
















import { errorApiRef, useApi } from '@backstage/core-plugin-api';
import { assertError } from '@backstage/errors';
import {
  catalogApiRef,
  humanizeEntityRef,
} from '@backstage/plugin-catalog-react';
import { Box, FormHelperText, Grid, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import React, { useCallback, useEffect, useState } from 'react';

import useAsync from 'react-use/lib/useAsync';
import YAML from 'yaml';
import { catalogImportApiRef } from '../../api';
import { useCatalogFilename } from '../../hooks';

import { BackButton, NextButton } from '../Buttons';

import { PreparePullRequestForm } from './PreparePullRequestForm';
import { PreviewCatalogInfoComponent } from './PreviewCatalogInfoComponent';
import { PreviewPullRequestComponent } from './PreviewPullRequestComponent';

const useStyles = makeStyles(theme => ({
  previewCard: {
    marginTop: theme.spacing(1),
  },
  previewCardContent: {
    paddingTop: 0,
  },
}));


































export function generateEntities(
  entities,
  componentName,
  owner,
) {
  return entities.map(e => ({
    ...e,
    apiVersion: e.apiVersion,
    kind: e.kind,
    metadata: {
      ...e.metadata,
      name: componentName,
    },
    spec: {
      ...e.spec,
      ...(owner ? { owner } : {}),
    },
  }));
}

/**
 * Prepares a pull request.
 *
 * @public
 */
export const StepPrepareCreatePullRequest = (
  props,
) => {
  const { analyzeResult, onPrepare, onGoBack, renderFormFields } = props;

  const classes = useStyles();
  const catalogApi = useApi(catalogApiRef);
  const catalogImportApi = useApi(catalogImportApiRef);
  const errorApi = useApi(errorApiRef);

  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState();

  const catalogFilename = useCatalogFilename();

  const {
    loading: prDefaultsLoading,
    value: prDefaults,
    error: prDefaultsError,
  } = useAsync(
    () => catalogImportApi.preparePullRequest(),
    [catalogImportApi.preparePullRequest],
  );

  useEffect(() => {
    if (prDefaultsError) {
      errorApi.post(prDefaultsError);
    }
  }, [prDefaultsError, errorApi]);

  const { loading: groupsLoading, value: groups } = useAsync(async () => {
    const groupEntities = await catalogApi.getEntities({
      filter: { kind: 'group' },
    });

    return groupEntities.items
      .map(e => humanizeEntityRef(e, { defaultKind: 'group' }))
      .sort();
  });

  const handleResult = useCallback(
    async (data) => {
      setSubmitted(true);

      try {
        const pr = await catalogImportApi.submitPullRequest({
          repositoryUrl: analyzeResult.url,
          title: data.title,
          body: data.body,
          fileContent: generateEntities(
            analyzeResult.generatedEntities,
            data.componentName,
            data.owner,
          )
            .map(e => YAML.stringify(e))
            .join('---\n'),
        });

        onPrepare(
          {
            type: 'repository',
            url: analyzeResult.url,
            integrationType: analyzeResult.integrationType,
            pullRequest: {
              url: pr.link,
            },
            locations: [
              {
                target: pr.location,
                entities: generateEntities(
                  analyzeResult.generatedEntities,
                  data.componentName,
                  data.owner,
                ).map(e => ({
                  kind: e.kind,
                  namespace: e.metadata.namespace,
                  name: e.metadata.name,
                })),
              },
            ],
          },
          { notRepeatable: true },
        );
      } catch (e) {
        assertError(e);
        setError(e.message);
        setSubmitted(false);
      }
    },
    [
      analyzeResult.generatedEntities,
      analyzeResult.integrationType,
      analyzeResult.url,
      catalogImportApi,
      onPrepare,
    ],
  );

  return (
    React.createElement(React.Fragment, null
      , React.createElement(Typography, null, "You entered a link to a "
              , analyzeResult.integrationType, " repository but a"   , ' '
        , React.createElement('code', null, catalogFilename), " could not be found. Use this form to open a Pull Request that creates one."

      )

      , !prDefaultsLoading && (
        React.createElement(PreparePullRequestForm, {
          onSubmit: handleResult,
          defaultValues: {
            title: prDefaults?.title ?? '',
            body: prDefaults?.body ?? '',
            owner:
              (analyzeResult.generatedEntities[0]?.spec?.owner ) || '',
            componentName:
              analyzeResult.generatedEntities[0]?.metadata?.name || '',
            useCodeowners: false,
          },
          render: ({ values, formState, register, setValue }) => (
            React.createElement(React.Fragment, null
              , renderFormFields({
                values,
                formState,
                register,
                setValue,
                groups: groups ?? [],
                groupsLoading,
              })

              , React.createElement(Box, { marginTop: 2,}
                , React.createElement(Typography, { variant: "h6",}, "Preview Pull Request"  )
              )

              , React.createElement(PreviewPullRequestComponent, {
                title: values.title,
                description: values.body,
                classes: {
                  card: classes.previewCard,
                  cardContent: classes.previewCardContent,
                },}
              )

              , React.createElement(Box, { marginTop: 2, marginBottom: 1,}
                , React.createElement(Typography, { variant: "h6",}, "Preview Entities" )
              )

              , React.createElement(PreviewCatalogInfoComponent, {
                entities: generateEntities(
                  analyzeResult.generatedEntities,
                  values.componentName,
                  values.owner,
                ),
                repositoryUrl: analyzeResult.url,
                classes: {
                  card: classes.previewCard,
                  cardContent: classes.previewCardContent,
                },}
              )

              , error && React.createElement(FormHelperText, { error: true,}, error)

              , React.createElement(Grid, { container: true, spacing: 0,}
                , onGoBack && (
                  React.createElement(BackButton, { onClick: onGoBack, disabled: submitted,} )
                )
                , React.createElement(NextButton, {
                  type: "submit",
                  disabled: Boolean(
                    formState.errors.title ||
                      formState.errors.body ||
                      formState.errors.owner,
                  ),
                  loading: submitted,}
                , "Create PR"

                )
              )
            )
          ),}
        )
      )
    )
  );
};
