import { RELATION_OWNED_BY } from '@backstage/catalog-model';
import {
  EntityRefLinks,
  entityRouteParams,
  getEntityRelations,
  entityRouteRef,
} from '@backstage/plugin-catalog-react';
import {
  Box,
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Chip,
} from '@material-ui/core';
import React from 'react';

import { Button, ItemCardHeader } from '@backstage/core-components';
import { useRouteRef } from '@backstage/core-plugin-api';





export const DomainCard = ({ entity }) => {
  const catalogEntityRoute = useRouteRef(entityRouteRef);

  const ownedByRelations = getEntityRelations(entity, RELATION_OWNED_BY);
  const url = catalogEntityRoute(entityRouteParams(entity));

  const owner = (
    React.createElement(EntityRefLinks, {
      entityRefs: ownedByRelations,
      defaultKind: "group",
      color: "inherit",}
    )
  );

  return (
    React.createElement(Card, null
      , React.createElement(CardMedia, null
        , React.createElement(ItemCardHeader, { title: entity.metadata.name, subtitle: owner,} )
      )
      , React.createElement(CardContent, null
        , entity.metadata.tags?.length ? (
          React.createElement(Box, null
            , entity.metadata.tags.map(tag => (
              React.createElement(Chip, { size: "small", label: tag, key: tag,} )
            ))
          )
        ) : null
        , entity.metadata.description
      )
      , React.createElement(CardActions, null
        , React.createElement(Button, { to: url, color: "primary",}, "Explore"

        )
      )
    )
  );
};
