import {
  Content,
  ContentHeader,
  Header,
  Page,
  SupportButton,
} from '@backstage/core-components';
import { configApiRef, useApi } from '@backstage/core-plugin-api';
import { Grid } from '@material-ui/core';
import React from 'react';
import { ImportInfoCard } from '../ImportInfoCard';
import { ImportStepper } from '../ImportStepper';

/**
 * The default catalog import page.
 *
 * @public
 */
export const DefaultImportPage = () => {
  const configApi = useApi(configApiRef);
  const appTitle = configApi.getOptional('app.title') || 'Backstage';

  return (
    React.createElement(Page, { themeId: "home",}
      , React.createElement(Header, { title: "Register an existing component"   ,} )
      , React.createElement(Content, null
        , React.createElement(ContentHeader, { title: `Start tracking your component in ${appTitle}`,}
          , React.createElement(SupportButton, null, "Start tracking your component in "
                 , appTitle, " by adding it to the software catalog."

          )
        )

        , React.createElement(Grid, { container: true, spacing: 2, direction: "row-reverse",}
          , React.createElement(Grid, { item: true, xs: 12, md: 4, lg: 6, xl: 8,}
            , React.createElement(ImportInfoCard, null )
          )

          , React.createElement(Grid, { item: true, xs: 12, md: 8, lg: 6, xl: 4,}
            , React.createElement(ImportStepper, null )
          )
        )
      )
    )
  );
};
