import {
  Header,
  Page,
  TabbedLayout,
  useSidebarPinState,
} from '@backstage/core-components';
import React from 'react';
import { useOutlet } from 'react-router';
import { useElementFilter } from '@backstage/core-plugin-api';
import { UserSettingsAuthProviders } from './AuthProviders';
import { UserSettingsFeatureFlags } from './FeatureFlags';
import { UserSettingsGeneral } from './General';
import { USER_SETTINGS_TAB_KEY, } from './UserSettingsTab';





export const SettingsPage = ({ providerSettings }) => {
  const { isMobile } = useSidebarPinState();
  const outlet = useOutlet();

  const tabs = useElementFilter(outlet, elements =>
    elements
      .selectByComponentData({
        key: USER_SETTINGS_TAB_KEY,
      })
      .getElements(),
  );

  return (
    React.createElement(Page, { themeId: "home",}
      , !isMobile && React.createElement(Header, { title: "Settings",} )
      , React.createElement(TabbedLayout, null
        , React.createElement(TabbedLayout.Route, { path: "general", title: "General",}
          , React.createElement(UserSettingsGeneral, null )
        )
        , React.createElement(TabbedLayout.Route, {
          path: "auth-providers",
          title: "Authentication Providers" ,}
        
          , React.createElement(UserSettingsAuthProviders, { providerSettings: providerSettings,} )
        )
        , React.createElement(TabbedLayout.Route, { path: "feature-flags", title: "Feature Flags" ,}
          , React.createElement(UserSettingsFeatureFlags, null )
        )

        , tabs.map((child, i) => (
          React.createElement(TabbedLayout.Route, { key: i, ...child.props,}
            , child
          )
        ))
      )
    )
  );
};
