import { Avatar, Link } from '@backstage/core-components';
import { Card, CardContent, CardHeader } from '@material-ui/core';

import { AutoCompleteIcon } from '../AutoCompleteIcon';

import { DateTime } from 'luxon';
import { PullRequestCardPolicies } from './PullRequestCardPolicies';
import { PullRequestCardReviewers } from './PullRequestCardReviewers';
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(
  theme => ({
    card: {
      backgroundColor:
        theme.palette.type === 'dark'
          ? theme.palette.grey[700]
          : theme.palette.common.white,
    },
    cardHeaderSimplified: {
      paddingBottom: theme.spacing(2),
    },
    cardHeaderAction: {
      display: 'flex',
      alignSelf: 'center',
      margin: 0,
    },
    content: {
      display: 'flex',
      flexDirection: 'row',
    },
    policies: {
      flex: 1,
    },
  }),
  { name: 'PullRequestCard' },
);






export const PullRequestCard = ({
  pullRequest,
  simplified,
}) => {
  const title = (
    React.createElement(Link, { to: pullRequest.link ?? '', title: pullRequest.description,}
      , pullRequest.title
    )
  );

  const repoLink = (
    React.createElement(Link, { to: pullRequest.repository?.url ?? '', color: "inherit",}
      , pullRequest.repository?.name
    )
  );

  const creationDate = pullRequest.creationDate
    ? DateTime.fromISO(pullRequest.creationDate).toRelative()
    : undefined;

  const subheader = (
    React.createElement('span', null
      , repoLink, " · "  , creationDate
    )
  );

  const avatar = (
    React.createElement(Avatar, {
      displayName: pullRequest.createdBy?.displayName,
      picture: pullRequest.createdBy?.imageUrl,
      customStyles: { width: '2.5rem', height: '2.5rem', fontSize: '1rem' },}
    )
  );

  const classes = useStyles();

  return (
    React.createElement(Card, {
      classes: { root: classes.card },
      'data-pull-request-id': pullRequest.pullRequestId,}
    
      , React.createElement(CardHeader, {
        avatar: avatar,
        title: title,
        subheader: subheader,
        action: 
          React.createElement(AutoCompleteIcon, { hasAutoComplete: pullRequest.hasAutoComplete,} )
        ,
        classes: {
          ...(simplified && { root: classes.cardHeaderSimplified }),
          action: classes.cardHeaderAction,
        },}
      )

      , !simplified && (
        React.createElement(CardContent, { className: classes.content,}
          , pullRequest.policies && (
            React.createElement(PullRequestCardPolicies, {
              policies: pullRequest.policies,
              className: classes.policies,}
            )
          )

          , pullRequest.reviewers && (
            React.createElement(PullRequestCardReviewers, { reviewers: pullRequest.reviewers,} )
          )
        )
      )
    )
  );
};
