export const MockGroups = [{ id: 'tech' }, { id: 'mock-group' }];




export const MockFilterProvider = ({
  children,
  ...context
}) => {
  const defaultContext = {
    pageFilters: {
      group: 'tech',
      project: null,
      duration: Duration.P90D,
      metric: null,
    },
    productFilters: [],
    setPageFilters: jest.fn(),
    setProductFilters: jest.fn(),
  };
  return (
    React.createElement(FilterContext.Provider, { value: { ...defaultContext, ...context },}
      , children
    )
  );
};




