import React, { useCallback, useState } from 'react';
import {
  List,
  TextField,
  IconButton,
  Grid,
  Typography,
} from '@material-ui/core';
import { EmptyFlags } from './EmptyFlags';
import { FlagItem } from './FeatureFlagsItem';

import {
  featureFlagsApiRef,
  FeatureFlagState,
  useApi,
} from '@backstage/core-plugin-api';
import { InfoCard } from '@backstage/core-components';
import ClearIcon from '@material-ui/icons/Clear';

export const UserSettingsFeatureFlags = () => {
  const featureFlagsApi = useApi(featureFlagsApiRef);
  const featureFlags = featureFlagsApi.getRegisteredFlags();

  const initialFlagState = Object.fromEntries(
    featureFlags.map(({ name }) => [name, featureFlagsApi.isActive(name)]),
  );

  const [state, setState] = useState(initialFlagState);
  const [filterInput, setFilterInput] = useState('');
  const inputRef = React.useRef();

  const toggleFlag = useCallback(
    (flagName) => {
      const newState = featureFlagsApi.isActive(flagName)
        ? FeatureFlagState.None
        : FeatureFlagState.Active;

      featureFlagsApi.save({
        states: { [flagName]: newState },
        merge: true,
      });

      setState(prevState => ({
        ...prevState,
        [flagName]: newState === FeatureFlagState.Active,
      }));
    },
    [featureFlagsApi],
  );

  if (!featureFlags.length) {
    return React.createElement(EmptyFlags, null );
  }

  const clearFilterInput = () => {
    setFilterInput('');
    inputRef?.current?.focus();
  };

  let filteredFeatureFlags = Array.from(featureFlags);

  const filterInputParts = filterInput
    .split(/\s/)
    .map(part => part.trim().toLocaleLowerCase('en-US'));

  filterInputParts.forEach(
    part =>
      (filteredFeatureFlags = filteredFeatureFlags.filter(featureFlag =>
        featureFlag.name.toLocaleLowerCase('en-US').includes(part),
      )),
  );

  const Header = () => (
    React.createElement(Grid, { container: true, style: { justifyContent: 'space-between' },}
      , React.createElement(Grid, { item: true, xs: 6, md: 8,}
        , React.createElement(Typography, { variant: "h5",}, "Feature Flags" )
      )
      , featureFlags.length >= 10 && (
        React.createElement(Grid, { item: true, xs: 6, md: 4,}
          , React.createElement(TextField, {
            label: "Filter",
            style: { display: 'flex', justifyContent: 'flex-end' },
            inputRef: ref => ref && ref.focus(),
            InputProps: {
              ...(filterInput.length && {
                endAdornment: (
                  React.createElement(IconButton, {
                    'aria-label': "Clear filter" ,
                    onClick: clearFilterInput,
                    edge: "end",}
                  
                    , React.createElement(ClearIcon, null )
                  )
                ),
              }),
            },
            onChange: e => setFilterInput(e.target.value),
            value: filterInput,}
          )
        )
      )
    )
  );

  return (
    React.createElement(InfoCard, { title: React.createElement(Header, null ),}
      , React.createElement(List, { dense: true,}
        , filteredFeatureFlags.map(featureFlag => {
          const enabled = Boolean(state[featureFlag.name]);

          return (
            React.createElement(FlagItem, {
              key: featureFlag.name,
              flag: featureFlag,
              enabled: enabled,
              toggleHandler: toggleFlag,}
            )
          );
        })
      )
    )
  );
};
