import React, { useState, useCallback } from 'react';

import { Card, CardHeader, Divider, CardContent } from '@material-ui/core';
import { Incidents } from '../Incident';
import { EscalationPolicy } from '../Escalation';
import useAsync from 'react-use/lib/useAsync';
import { Alert } from '@material-ui/lab';
import { pagerDutyApiRef, UnauthorizedError } from '../../api';
import AlarmAddIcon from '@material-ui/icons/AlarmAdd';
import { MissingTokenError } from '../Errors/MissingTokenError';
import WebIcon from '@material-ui/icons/Web';
import DateRangeIcon from '@material-ui/icons/DateRange';
import { usePagerdutyEntity } from '../../hooks';
import { PAGERDUTY_INTEGRATION_KEY } from '../constants';
import { TriggerDialog } from '../TriggerDialog';
import { ChangeEvents } from '../ChangeEvents';

import { useApi } from '@backstage/core-plugin-api';
import {
  Progress,
  HeaderIconLinkRow,

  TabbedCard,
  CardTab,
} from '@backstage/core-components';

export const isPluginApplicableToEntity = (entity) =>
  Boolean(entity.metadata.annotations?.[PAGERDUTY_INTEGRATION_KEY]);

export const PagerDutyCard = () => {
  const { integrationKey } = usePagerdutyEntity();
  const api = useApi(pagerDutyApiRef);
  const [refreshIncidents, setRefreshIncidents] = useState(false);
  const [refreshChangeEvents, setRefreshChangeEvents] =
    useState(false);
  const [dialogShown, setDialogShown] = useState(false);

  const showDialog = useCallback(() => {
    setDialogShown(true);
  }, [setDialogShown]);
  const hideDialog = useCallback(() => {
    setDialogShown(false);
  }, [setDialogShown]);

  const handleRefresh = useCallback(() => {
    setRefreshIncidents(x => !x);
    setRefreshChangeEvents(x => !x);
  }, []);

  const {
    value: service,
    loading,
    error,
  } = useAsync(async () => {
    const services = await api.getServiceByIntegrationKey(
      integrationKey ,
    );

    return {
      id: services[0].id,
      name: services[0].name,
      url: services[0].html_url,
      policyId: services[0].escalation_policy.id,
      policyLink: services[0].escalation_policy.html_url,
    };
  });

  if (error instanceof UnauthorizedError) {
    return React.createElement(MissingTokenError, null );
  }

  if (error) {
    return (
      React.createElement(Alert, { severity: "error",}, "Error encountered while fetching information. "
             , error.message
      )
    );
  }

  if (loading) {
    return React.createElement(Progress, null );
  }

  const serviceLink = {
    label: 'Service Directory',
    href: service.url,
    icon: React.createElement(WebIcon, null ),
  };

  const triggerLink = {
    label: 'Create Incident',
    onClick: showDialog,
    icon: React.createElement(AlarmAddIcon, null ),
    color: 'secondary',
  };

  const escalationPolicyLink = {
    label: 'Escalation Policy',
    href: service.policyLink,
    icon: React.createElement(DateRangeIcon, null ),
  };

  return (
    React.createElement(React.Fragment, null
      , React.createElement(Card, { 'data-testid': "pagerduty-card",}
        , React.createElement(CardHeader, {
          title: "PagerDuty",
          subheader: 
            React.createElement(HeaderIconLinkRow, {
              links: [serviceLink, triggerLink, escalationPolicyLink],}
            )
          ,}
        )
        , React.createElement(Divider, null )
        , React.createElement(CardContent, null
          , React.createElement(TabbedCard, null
            , React.createElement(CardTab, { label: "Incidents",}
              , React.createElement(Incidents, {
                serviceId: service.id,
                refreshIncidents: refreshIncidents,}
              )
            )
            , React.createElement(CardTab, { label: "Change Events" ,}
              , React.createElement(ChangeEvents, {
                serviceId: service.id,
                refreshEvents: refreshChangeEvents,}
              )
            )
          )
          , React.createElement(EscalationPolicy, { policyId: service.policyId,} )
        )
      )
      , React.createElement(TriggerDialog, {
        'data-testid': "trigger-dialog",
        showDialog: dialogShown,
        handleDialog: hideDialog,
        onIncidentCreated: handleRefresh,}
      )
    )
  );
};
