import React, { useState } from 'react';
import { getEntitySourceLocation } from '@backstage/catalog-model';
import { useEntity } from '@backstage/plugin-catalog-react';
import { Alert } from '@material-ui/lab';
import { Button } from '@material-ui/core';
import GitHubIcon from '@material-ui/icons/GitHub';
import {

  GoCdBuildResultStatus,
  toBuildResultStatus,


} from '../../api/gocdApi.model';
import { DateTime } from 'luxon';
import {
  Table,

  Link,
  SubvalueCell,
  StatusOK,
  StatusWarning,
  StatusAborted,
  StatusError,
  StatusRunning,
  StatusPending,
} from '@backstage/core-components';








const renderTrigger = (build) => {
  const subvalue = (
    React.createElement(React.Fragment, null
      , build.pipeline
      , React.createElement('br', null )
      , build.author
    )
  );
  return React.createElement(SubvalueCell, { value: build.message, subvalue: subvalue,} );
};

const renderStages = (build) => {
  return build.stages.map(s => {
    switch (s.status) {
      case GoCdBuildResultStatus.successful: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusOK, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
      case GoCdBuildResultStatus.error: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusError, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
      case GoCdBuildResultStatus.running: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusRunning, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
      case GoCdBuildResultStatus.aborted: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusAborted, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
      case GoCdBuildResultStatus.pending: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusPending, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
      default: {
        return (
          React.createElement(React.Fragment, null
            , React.createElement(StatusWarning, null, s.text)
            , React.createElement('br', null )
            , React.createElement('br', null )
          )
        );
      }
    }
  });
};

const renderSource = (build) => {
  return (
    React.createElement(Button, {
      href: build.source,
      target: "_blank",
      rel: "noreferrer",
      startIcon: React.createElement(GitHubIcon, null ),}
    
      , build.commitHash
    )
  );
};

const renderId = (
  goCdBaseUrl,
  build,
) => {
  const goCdBuildUrl = `${goCdBaseUrl}/go/pipelines/value_stream_map/${build.buildSlug}`;
  return (
    React.createElement(SubvalueCell, {
      value: 
        React.createElement(Link, { to: goCdBuildUrl, target: "_blank", rel: "noreferrer",}
          , build.id
        )
      ,
      subvalue: 
        build.triggerTime &&
        DateTime.fromMillis(build.triggerTime).toLocaleString(
          DateTime.DATETIME_MED,
        )
      ,}
    )
  );
};

const renderError = (error) => {
  return React.createElement(Alert, { severity: "error",}, error.message);
};

const toBuildResults = (
  entity,
  builds,
) => {
  // TODO(julioz): What if not git 'type'?
  const entitySourceLocation =
    getEntitySourceLocation(entity).target.split('/tree')[0];
  return builds.map(build => ({
    id: build.counter,
    source: `${entitySourceLocation}/commit/${build.build_cause?.material_revisions[0]?.modifications[0].revision}`,
    stages: build.stages.map(s => ({
      status: toBuildResultStatus(s.status),
      text: s.name,
    })),
    buildSlug: `${build.name}/${build.counter}`,
    message:
      build.build_cause?.material_revisions[0]?.modifications[0].comment ?? '',
    pipeline: build.name,
    author:
      build.build_cause?.material_revisions[0]?.modifications[0].user_name,
    commitHash: build.label,
    triggerTime: build.scheduled_date,
  }));
};

export const GoCdBuildsTable = (props) => {
  const { pipelineHistory, loading, error } = props;
  const { entity } = useEntity();
  const [, setSelectedSearchTerm] = useState('');

  const columns = [
    {
      title: 'ID',
      field: 'id',
      highlight: true,
      render: build => renderId(props.goCdBaseUrl, build),
    },
    {
      title: 'Trigger',
      customFilterAndSearch: (query, row) =>
        `${row.message} ${row.pipeline} ${row.author}`
          .toLocaleUpperCase('en-US')
          .includes(query.toLocaleUpperCase('en-US')),
      field: 'message',
      highlight: true,
      render: build => renderTrigger(build),
    },
    {
      title: 'Stages',
      field: 'status',
      render: build => renderStages(build),
    },
    {
      title: 'Source',
      field: 'source',
      render: build => renderSource(build),
    },
  ];

  return (
    React.createElement(React.Fragment, null
      , error && renderError(error)
      , !error && (
        React.createElement(Table, {
          title: "GoCD builds" ,
          isLoading: loading,
          options: {
            search: true,
            searchAutoFocus: true,
            debounceInterval: 800,
            paging: true,
            padding: 'dense',
            pageSizeOptions: [5, 10, 20, 50],
            showFirstLastPageButtons: false,
            pageSize: 20,
          },
          columns: columns,
          data: 
            pipelineHistory
              ? toBuildResults(entity, pipelineHistory.pipelines) || []
              : []
          ,
          onSearchChange: (searchTerm) =>
            setSelectedSearchTerm(searchTerm)
          ,}
        )
      )
    )
  );
};
