import React, { useEffect } from 'react';
import { useWorkflowRuns, } from '../useWorkflowRuns';
import { WorkflowRunsTable } from '../WorkflowRunsTable';
import { useEntity } from '@backstage/plugin-catalog-react';
import { WorkflowRunStatus } from '../WorkflowRunStatus';
import { makeStyles, LinearProgress } from '@material-ui/core';
import ExternalLinkIcon from '@material-ui/icons/Launch';
import { CLOUDBUILD_ANNOTATION } from '../useProjectName';

import {
  InfoCard,
  Link,
  StructuredMetadataTable,
  WarningPanel,
} from '@backstage/core-components';
import { errorApiRef, useApi } from '@backstage/core-plugin-api';

const useStyles = makeStyles({
  externalLinkIcon: {
    fontSize: 'inherit',
    verticalAlign: 'bottom',
  },
});

const WidgetContent = ({
  error,
  loading,
  lastRun,
  branch,
}




) => {
  const classes = useStyles();
  if (error)
    return React.createElement(WarningPanel, null, "Couldn't fetch latest "   , branch, " run" );
  if (loading) return React.createElement(LinearProgress, null );
  return (
    React.createElement(StructuredMetadataTable, {
      metadata: {
        status: (
          React.createElement(React.Fragment, null
            , React.createElement(WorkflowRunStatus, { status: lastRun.status,} )
          )
        ),
        message: lastRun.message,
        url: (
          React.createElement(Link, { to: lastRun.googleUrl ?? '',}, "See more on Google"
               , ' '
            , React.createElement(ExternalLinkIcon, { className: classes.externalLinkIcon,} )
          )
        ),
      },}
    )
  );
};

export const LatestWorkflowRunCard = ({
  branch = 'master',
}

) => {
  const { entity } = useEntity();
  const errorApi = useApi(errorApiRef);
  const projectId = entity?.metadata.annotations?.[CLOUDBUILD_ANNOTATION] || '';

  const [{ runs, loading, error }] = useWorkflowRuns({
    projectId,
  });
  const lastRun = runs?.[0] ?? ({} );
  useEffect(() => {
    if (error) {
      errorApi.post(error);
    }
  }, [error, errorApi]);

  return (
    React.createElement(InfoCard, { title: `Last ${branch} build`,}
      , React.createElement(WidgetContent, {
        error: error,
        loading: loading,
        branch: branch,
        lastRun: lastRun,}
      )
    )
  );
};

export const LatestWorkflowsForBranchCard = ({
  branch = 'master',
}

) => {
  const { entity } = useEntity();

  return (
    React.createElement(InfoCard, { title: `Last ${branch} build`,}
      , React.createElement(WorkflowRunsTable, { entity: entity,} )
    )
  );
};
