


















import { stringArrayHas } from '../../../../utils';






export function createAssignedToTeamFilter(
  filter,
) {
  return (pullRequest) => {
    const reviewerIds = pullRequest.reviewers?.map(reviewer => reviewer.id);

    if (!reviewerIds) {
      return false;
    }

    return stringArrayHas(reviewerIds, filter.teamId, true);
  };
}
