import { LinearProgress, makeStyles, } from '@material-ui/core';
import ExternalLinkIcon from '@material-ui/icons/Launch';
import { DateTime, Duration } from 'luxon';
import React from 'react';
import { JenkinsRunStatus } from '../BuildsPage/lib/Status';
import { ErrorType, useBuilds } from '../useBuilds';
import {
  InfoCard,

  Link,
  StructuredMetadataTable,
  WarningPanel,
} from '@backstage/core-components';


const useStyles = makeStyles({
  externalLinkIcon: {
    fontSize: 'inherit',
    verticalAlign: 'bottom',
  },
});

const WidgetContent = ({
  loading,
  latestRun,
}



) => {
  const classes = useStyles();
  if (loading || !latestRun) return React.createElement(LinearProgress, null );
  const displayDate = DateTime.fromMillis(
    latestRun.lastBuild.timestamp,
  ).toRelative();
  const displayDuration =
    (latestRun.lastBuild.building ? 'Running for ' : '') +
    DateTime.local()
      .minus(Duration.fromMillis(latestRun.lastBuild.duration))
      .toRelative({ locale: 'en' })
      ?.replace(' ago', '');

  return (
    React.createElement(StructuredMetadataTable, {
      metadata: {
        status: (
          React.createElement(React.Fragment, null
            , React.createElement(JenkinsRunStatus, { status: latestRun.lastBuild.status,} )
          )
        ),
        build: latestRun.fullDisplayName,
        'latest run': displayDate,
        duration: displayDuration,
        link: (
          React.createElement(Link, { to: latestRun.lastBuild.url,}, "See more on Jenkins"
               , ' '
            , React.createElement(ExternalLinkIcon, { className: classes.externalLinkIcon,} )
          )
        ),
      },}
    )
  );
};

const JenkinsApiErrorPanel = ({
  message,
  errorType,
}


) => {
  let title = undefined;
  if (errorType === ErrorType.CONNECTION_ERROR) {
    title = "Can't connect to Jenkins";
  } else if (errorType === ErrorType.NOT_FOUND) {
    title = "Can't find Jenkins project";
  }

  return React.createElement(WarningPanel, { severity: "error", title: title, message: message,} );
};

export const LatestRunCard = ({
  branch = 'master',
  variant,
}


) => {
  const [{ projects, loading, error }] = useBuilds({ branch });
  const latestRun = projects?.[0];
  return (
    React.createElement(InfoCard, { title: `Latest ${branch} build`, variant: variant,}
      , !error ? (
        React.createElement(WidgetContent, {
          loading: loading,
          branch: branch,
          latestRun: latestRun,}
        )
      ) : (
        React.createElement(JenkinsApiErrorPanel, {
          message: error.message,
          errorType: error.errorType,}
        )
      )
    )
  );
};
