import React from 'react';
import {
  StatusPending,
  StatusRunning,
  StatusOK,
  StatusWarning,
  StatusAborted,
  StatusError,
} from '@backstage/core-components';

export const JenkinsRunStatus = ({
  status,
}

) => {
  if (status === undefined) return null;
  switch (status.toLocaleLowerCase('en-US')) {
    case 'queued':
    case 'scheduled':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusPending, null ), " Queued"
        )
      );
    case 'running':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusRunning, null ), " In progress"
        )
      );
    case 'unstable':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusWarning, null ), " Unstable"
        )
      );
    case 'failure':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusError, null ), " Failed"
        )
      );
    case 'success':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusOK, null ), " Completed"
        )
      );
    case 'aborted':
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusAborted, null ), " Aborted"
        )
      );
    default:
      return (
        React.createElement(React.Fragment, null
          , React.createElement(StatusWarning, null ), " " , status
        )
      );
  }
};
