import {
  Checkbox,
  Chip,
  FormControl,
  InputLabel,
  ListItemText,
  makeStyles,
  MenuItem,
  Select,
} from '@material-ui/core';
import React, { } from 'react';
import useEffectOnce from 'react-use/lib/useEffectOnce';
import {
  SearchTypeAccordion,
  SearchTypeAccordionProps,
} from './SearchType.Accordion';
import { SearchTypeTabs, SearchTypeTabsProps } from './SearchType.Tabs';
import { useSearch } from '@backstage/plugin-search-react';

const useStyles = makeStyles(theme => ({
  label: {
    textTransform: 'capitalize',
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
    marginTop: theme.spacing(1),
  },
  chip: {
    margin: 2,
  },
}));

/**
 * @public
 */







const SearchType = (props) => {
  const { className, defaultValue, name, values = [] } = props;
  const classes = useStyles();
  const { types, setTypes } = useSearch();

  useEffectOnce(() => {
    if (!types.length) {
      if (defaultValue && Array.isArray(defaultValue)) {
        setTypes(defaultValue);
      } else if (defaultValue) {
        setTypes([defaultValue]);
      }
    }
  });

  const handleChange = (e) => {
    const value = e.target.value ;
    setTypes(value );
  };

  return (
    React.createElement(FormControl, {
      className: className,
      variant: "filled",
      fullWidth: true,
      'data-testid': "search-typefilter-next",}
    
      , React.createElement(InputLabel, { className: classes.label, margin: "dense",}
        , name
      )
      , React.createElement(Select, {
        multiple: true,
        variant: "outlined",
        value: types,
        onChange: handleChange,
        placeholder: "All Results" ,
        renderValue: selected => (
          React.createElement('div', { className: classes.chips,}
            , (selected ).map(value => (
              React.createElement(Chip, {
                key: value,
                label: value,
                className: classes.chip,
                size: "small",}
              )
            ))
          )
        ),}
      
        , values.map((value) => (
          React.createElement(MenuItem, { key: value, value: value,}
            , React.createElement(Checkbox, { checked: types.indexOf(value) > -1,} )
            , React.createElement(ListItemText, { primary: value,} )
          )
        ))
      )
    )
  );
};

/**
 * A control surface for the search query's "types" property, displayed as a
 * single-select collapsible accordion suitable for use in faceted search UIs.
 * @public
 */
SearchType.Accordion = (props) => {
  return React.createElement(SearchTypeAccordion, { ...props,} );
};

/**
 * A control surface for the search query's "types" property, displayed as a
 * tabs suitable for use in faceted search UIs.
 * @public
 */
SearchType.Tabs = (props) => {
  return React.createElement(SearchTypeTabs, { ...props,} );
};

export { SearchType };
;
