export const EngineerThreshold = 0.5;

export var ChangeThreshold; (function (ChangeThreshold) {
  const upper = 0.05; ChangeThreshold[ChangeThreshold["upper"] = upper] = "upper";
  const lower = -0.05; ChangeThreshold[ChangeThreshold["lower"] = lower] = "lower";
})(ChangeThreshold || (ChangeThreshold = {}));

export var GrowthType; (function (GrowthType) {
  const Negligible = 0; GrowthType[GrowthType["Negligible"] = Negligible] = "Negligible";
  const Savings = Negligible + 1; GrowthType[GrowthType["Savings"] = Savings] = "Savings";
  const Excess = Savings + 1; GrowthType[GrowthType["Excess"] = Excess] = "Excess";
})(GrowthType || (GrowthType = {}));
