import React, { Fragment } from 'react';
import { Avatar, Box, Collapse, Divider, Tooltip } from '@material-ui/core';
import { default as AcceptIcon } from '@material-ui/icons/Check';
import { default as DismissIcon } from '@material-ui/icons/Delete';
import { default as SnoozeIcon } from '@material-ui/icons/AccessTime';
import { ActionItemCard } from '../ActionItems';
import { AlertStatus } from '../../types';
import { useActionItemCardStyles as useStyles } from '../../utils/styles';








const AlertGroup = ({ alerts, status, title, icon }) => {
  const classes = useStyles();
  return (
    React.createElement(Box, { p: 1,}
      , alerts.map((alert, index) => (
        React.createElement(Fragment, { key: `alert-${status}-${index}`,}
          , React.createElement(ActionItemCard, {
            disableScroll: true,
            alert: alert,
            avatar: 
              React.createElement(Tooltip, { title: title,}
                , React.createElement(Avatar, { className: classes.avatar,}, icon)
              )
            ,}
          )
          , index < alerts.length - 1 && React.createElement(Divider, null )
        )
      ))
    )
  );
};








export const AlertStatusSummary = ({
  open,
  snoozed,
  accepted,
  dismissed,
}) => {
  const isSnoozedListDisplayed = !!snoozed.length;
  const isAcceptedListDisplayed = !!accepted.length;
  const isDismissedListDisplayed = !!dismissed.length;

  return (
    React.createElement(Collapse, { in: open,}
      , isAcceptedListDisplayed && (
        React.createElement(AlertGroup, {
          title: "Accepted",
          alerts: accepted,
          status: AlertStatus.Accepted,
          icon: 
            React.createElement(AcceptIcon, {
              role: "img",
              'aria-hidden': false,
              'aria-label': AlertStatus.Accepted,}
            )
          ,}
        )
      )
      , isSnoozedListDisplayed && (
        React.createElement(AlertGroup, {
          title: "Snoozed",
          alerts: snoozed,
          status: AlertStatus.Snoozed,
          icon: 
            React.createElement(SnoozeIcon, {
              role: "img",
              'aria-hidden': false,
              'aria-label': AlertStatus.Snoozed,}
            )
          ,}
        )
      )
      , isDismissedListDisplayed && (
        React.createElement(AlertGroup, {
          title: "Dismissed",
          alerts: dismissed,
          status: AlertStatus.Dismissed,
          icon: 
            React.createElement(DismissIcon, {
              role: "img",
              'aria-hidden': false,
              'aria-label': AlertStatus.Dismissed,}
            )
          ,}
        )
      )
    )
  );
};
