import * as React from 'react';

import { Chip, Typography, Grid } from '@material-ui/core';
import EmptyStateImage from '../../assets/emptystate.svg';
import { Table, InfoCard } from '@backstage/core-components';





const columns = [
  {
    title: 'cluster',
    width: '15%',
    render: (detectedError) => detectedError.cluster,
  },
  {
    title: 'kind',
    width: '15%',
    render: (detectedError) => detectedError.kind,
  },
  {
    title: 'name',
    width: '30%',
    render: (detectedError) => {
      const errorCount = detectedError.names.length;

      if (errorCount === 0) {
        // This shouldn't happen
        return null;
      }

      const displayName = detectedError.names[0];

      const otherErrorCount = errorCount - 1;

      return (
        React.createElement(React.Fragment, null
          , displayName, ' '
          , otherErrorCount > 0 && (
            React.createElement(Chip, {
              label: `+ ${otherErrorCount} other${
                otherErrorCount > 1 ? 's' : ''
              }`,
              size: "small",}
            )
          )
        )
      );
    },
  },
  {
    title: 'messages',
    width: '40%',
    render: (detectedError) => (
      React.createElement(React.Fragment, null
        , detectedError.message.map((m, i) => (
          React.createElement('div', { key: i,}, m)
        ))
      )
    ),
  },
];

const sortBySeverity = (a, b) => {
  if (a.severity < b.severity) {
    return 1;
  } else if (b.severity < a.severity) {
    return -1;
  }
  return 0;
};

export const ErrorEmptyState = () => {
  return (
    React.createElement(Grid, {
      container: true,
      justifyContent: "space-around",
      direction: "row",
      alignItems: "center",
      spacing: 2,}
    
      , React.createElement(Grid, { item: true, xs: 4,}
        , React.createElement(Typography, { variant: "h5",}, "Nice! There are no errors to report!"

        )
      )
      , React.createElement(Grid, { item: true, xs: 4,}
        , React.createElement('img', {
          src: EmptyStateImage,
          alt: "EmptyState",
          'data-testid': "emptyStateImg",}
        )
      )
    )
  );
};

export const ErrorReporting = ({ detectedErrors }) => {
  const errors = Array.from(detectedErrors.values())
    .flat()
    .sort(sortBySeverity);

  return (
    React.createElement(React.Fragment, null
      , errors.length === 0 ? (
        React.createElement(InfoCard, { title: "Error Reporting" ,}
          , React.createElement(ErrorEmptyState, null )
        )
      ) : (
        React.createElement(Table, {
          title: "Error Reporting" ,
          data: errors,
          columns: columns,
          options: { paging: true, search: false },}
        )
      )
    )
  );
};
