


















import { getResetStateWithoutInitial } from '../../utils/loading';










export const mapFiltersToProps

 = ({ pageFilters, setPageFilters }) => ({
  ...pageFilters,
  setGroup: (group) =>
    setPageFilters({
      ...pageFilters,
      group: group.id,
      project: null,
    }),
});

export const mapLoadingToProps

 = ({ actions, dispatch }) => ({
  loadingActions: actions,
  dispatchReset: (loadingActions) =>
    dispatch(getResetStateWithoutInitial(loadingActions)),
});
