























export const mapFiltersToProps = ({
  pageFilters,
  setPageFilters,
}) => ({
  ...pageFilters,
  project: pageFilters.project || 'all',
  setDuration: (duration) =>
    setPageFilters({
      ...pageFilters,
      duration,
    }),
  setProject: (project) =>
    setPageFilters({
      ...pageFilters,
      project: project === 'all' ? null : project,
    }),
  setMetric: (metric) =>
    setPageFilters({
      ...pageFilters,
      metric: metric,
    }),
});
